import { IUser } from './../../../model/IUser';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'sp-children-view',
  templateUrl: './children-view.component.html',
  styleUrls: ['./children-view.component.css']
})
export class ChildrenViewComponent implements OnInit {

  @Input()
  children: IUser[] = [];

  @Input()
  title = '';

  @Output()
  child: EventEmitter<IUser> = new EventEmitter <IUser>();

  constructor() { }

  ngOnInit() {
  }

  selectChild(user: IUser) {
    this.child.emit(user);
  }

}



