import { IUser } from './../model/IUser';
import { STATES } from './../states.constant';
import { NgForm } from '@angular/forms/src/directives';
import { ParseService } from '../services/parse.service';
import { LoadingService } from './../services/loading.service';
import { BODY_CLASSES } from './../services/route.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { RouteService } from '../services/route.service';
import { FormGroup, FormBuilder, Validators, FormControl, AbstractControl } from '@angular/forms';
import { ToastsManager } from 'ng2-toastr';
import { getSrc } from '../md5';

const PASSWORD_REGEX = /^(?=.[A-Za-z])(?=.\d)[A-Za-z\d]{4,20}/;

@Component({
  selector: 'sp-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  changePasswordForm: FormGroup;
  personalInfoForm: FormGroup;

  passwordControl = new FormControl('', [Validators.required]);
  confirmPasswordControl = new FormControl('', [Validators.required, this.validatePasswordConfirmation.bind(this)]);

  uploadProfilePictureControl: FormControl = new FormControl();

  isLoading = false;

  user: any = this.ps.currentUser;
  imgUrl: string;
  states = STATES;

  constructor(
    private rs: RouteService,
    private fb: FormBuilder,
    private ps: ParseService,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private ls: LoadingService) {

    this.toastr.setRootViewContainerRef(vRef);
    this.createForm();
  }

  createForm() {
    this.changePasswordForm = this.fb.group({
      password: this.passwordControl,
      confirmPassword: this.confirmPasswordControl
    });

    this.personalInfoForm = this.fb.group({
      firstName: [this.user.get('firstName'), Validators.required],
      lastName: [this.user.get('lastName'), Validators.required],
      city: [this.user.get('city'), Validators.required],
      state: [this.user.get('state'), Validators.required]
    });
  }

  ngOnInit() {
    this.rs.setBc(BODY_CLASSES.normal);
    if (this.user.has('img')) {
      this.imgUrl = this.user.get('img').url();
    } else {
      this.imgUrl = getSrc(this.user.get('email'));
    }
  }

  saveProfile() {
    const profile: any = {};

    profile.firstName = this.personalInfoForm.controls.firstName.value;
    profile.lastName = this.personalInfoForm.controls.lastName.value;
    profile.city = this.personalInfoForm.controls.city.value;
    profile.state = this.personalInfoForm.controls.state.value;

    this.isLoading = true;

    this.ps.currentUser.set('firstName', profile.firstName);
    this.ps.currentUser.set('lastName', profile.lastName);
    this.ps.currentUser.set('city', profile.city);
    this.ps.currentUser.set('state', profile.state);

    this.ls.setLoading(true);

    this.ps.save(this.ps.currentUser).subscribe(
      done => {
        this.ls.setLoading(false);
        location.href = '/setting';
      },
      err => {
        this.isLoading = false;
        this.ls.setLoading(false);
        this.toastr.error(err.message);
        console.log(err);
      }
    );
  }

  fileChange(event) {
    const fileList: FileList = event.target.files;
    const file: File = fileList[0];
    console.log(file);
    this.ls.setLoading(true);
    this.isLoading = true;
    this.ps.changeAvatar(file).subscribe(
      done => {
        this.ls.setLoading(false);
        location.href = '/setting';
      },
      err => {
        this.isLoading = false;
        this.ls.setLoading(false);
        this.toastr.error(err.message);
        console.log(err);
      }
    );
  }

  changePassword(form: NgForm) {
    this.isLoading = true;
    this.ls.setLoading(true);
    const password = this.passwordControl.value;
    this.ps.changePassword(password).subscribe(
      done => {
        this.ls.setLoading(false);
        this.isLoading = false;
        form.resetForm();
        this.toastr.success('Password changed');
      },
      err => {
        this.ls.setLoading(false);
        this.isLoading = false;
        this.toastr.error(err.message, 'Error');
        console.log(err);
      }
    );
  }

  validatePasswordConfirmation(control: FormControl) {
    if (this.changePasswordForm) {
      return control.value === this.changePasswordForm.get('password').value ? null : { notSame: true };
    }
  }

}

export class PasswordValidation {

  static MatchPassword(AC: AbstractControl) {
    let password = AC.get('password').value; // to get value in input tag
    let confirmPassword = AC.get('confirmPassword').value; // to get value in input tag
    if (password !== confirmPassword) {
      console.log('false');
      AC.get('confirmPassword').setErrors({ MatchPassword: true });
    } else {
      console.log('true');
      return null;
    }
  }
}



