import { GET_NEWS_ACTION, ADD_NEWS_ACTION } from './../../news/news.component';
import { ADD_USER_COUNT_ACTION } from './../../services/user.service';
import { ADD_NEW_PICKUP, NEW_PICKUP_ADDED, ADD_ALL_PICKUPS, SET_CURRENT_SELECTED_USER } from '../../profile/store/profile.actions';
import { SET_CURRENT_SELECTED_USER_ACTION } from './../../user/user-action';
import { REPORTS_FETCHED_ACTION } from './../../actions/report-actions';
import { UsersState, INIT_USER_PROFILE } from './../../states/data-state';
import {  INIT_ACTION,
          LoadDashBoardAction,
          MailLoadedAction,
          MAIL_LOADED_ACTION,
          VIEW_MESSAGE_LOADED_ACTION,
          STREAM_NEW_MESSAGE_ACTION,
          LoadedViewMessageAction,
          ChangeCurrentMessageSection,
          MarkAsReadAction } from './../message-action';
import {  CHANGE_CURRENT_MESSAGE_SECTION_ACTION,
          MESSAGE_SENT_ACTION,
          SEND_MESSAGE_ACTION,
          SendMessageAction,
          MessageSentAction,
          MARK_AS_READ_ACTION } from '../message-action';

import { IMail } from './../../model/IMessage';
import { INITIAL_APP_STATE, AppState } from './../message-state';

import { Action } from '@ngrx/store';
import * as _ from 'lodash';
import { StreamNewMessageAction } from '../message-action';
import { SetCurrentSelectedUser } from '../../reducers/user/setCurrentUser';
import { ReportsFetched } from '../../reducers/reports/reports-fetched';
import { AddNewPickupReducer, NewPickupAddedReducer, AddAllPickupsReducer, SetCurrentSelectedUserReducer } from '../../profile/store/profile.reducer';
import { addUserCountReducer } from '../../services/user.service';
import { addNewsReducer } from '../../news/news.component';

export function messageStoreReducer(state: AppState = INITIAL_APP_STATE,
  action: Action): AppState {
  switch (action.type) {
    case CHANGE_CURRENT_MESSAGE_SECTION_ACTION:
      return changeCurrentMessageSection(state, action);
    case MAIL_LOADED_ACTION:
      return handleLoadMainAction(state, action);
    case VIEW_MESSAGE_LOADED_ACTION:
      return handleViewMessageAction(state, action.payload);
    case SEND_MESSAGE_ACTION:
      return handleSendMessage(state, action);
    case MESSAGE_SENT_ACTION:
      return handleMessageSent(state, action);
    case MARK_AS_READ_ACTION:
      return handleMarkAsRead(state, action);
    case STREAM_NEW_MESSAGE_ACTION:
      return handleStreamedMessage(state, action);
    case REPORTS_FETCHED_ACTION:
      return ReportsFetched(state, action);
    case SET_CURRENT_SELECTED_USER_ACTION:
      return SetCurrentSelectedUser(state, action);
    case ADD_NEW_PICKUP:
      return AddNewPickupReducer(state, action);
    case NEW_PICKUP_ADDED:
      return NewPickupAddedReducer(state, action);
    case ADD_ALL_PICKUPS:
      return AddAllPickupsReducer(state, action);
    case SET_CURRENT_SELECTED_USER:
      return SetCurrentSelectedUserReducer(state, action);
    case ADD_USER_COUNT_ACTION:
      return addUserCountReducer(state, action);
    case ADD_NEWS_ACTION:
      return addNewsReducer(state, action);
    default:
      return state;
  }
}



function handleStreamedMessage(state: AppState, action): AppState {
  const newState: AppState = _.cloneDeep(state);
  console.log(action);
  const inbox: IMail[] = _.values(newState.dataState.inbox);
  inbox.unshift(action.payload.item);
  newState.dataState.inbox = _.keyBy(inbox, 'objectId');
  newState.uiState.currentUnread = getUnreadCount(inbox);
  return newState;
}

function handleMarkAsRead(state: AppState, action): AppState {
  const newState: AppState = _.cloneDeep(state);
  newState.dataState.inbox[action.payload].isRead = true;

  const inbox = _.values(newState.dataState.inbox);
  newState.uiState.currentUnread = getUnreadCount(inbox);

  return newState;
}

function handleSendMessage(state: AppState, action: SendMessageAction): AppState {
  const newState: AppState = _.cloneDeep(state);
  newState.uiState.currentMailSection = 0;
  return newState;
}

function handleMessageSent(state: AppState, action: MessageSentAction): AppState {
  const newState: AppState = _.cloneDeep(state);
  newState.uiState.currentMailSection = 0;
  const outbox: IMail[] = _.values(newState.dataState.outbox);
  outbox.unshift(action.payload);
  newState.dataState.outbox = _.keyBy(outbox, 'objectId');
  return newState;
}

function changeCurrentMessageSection(state: AppState, action: ChangeCurrentMessageSection): AppState {
  const payload: number = action.payload;
  const newState: AppState = _.cloneDeep(state);

  newState.uiState.currentMailSection = payload;

  return newState;
}

function handleViewMessageAction(state: AppState, action: LoadedViewMessageAction): AppState {
  const payload: any = action;
  const newState: AppState = _.cloneDeep(state);

  const inMail: IMail = newState.dataState.inbox[payload.id];
  const outMail: IMail = newState.dataState.outbox[payload.id];

  const section: number = payload.section;

  let mail: IMail;


  if (section === 0) {
    mail = inMail;

  } else if (section === 1) {
    mail = outMail;
  }

  newState.viewMessageState.message = mail;

  newState.uiState.currentMailSection = 2;

  return newState;
}

function handleLoadMainAction(state: AppState, action: MailLoadedAction): AppState {
  const mailsData = action.payload;
  const newState: AppState = _.cloneDeep(state);

  const inbox = newState.dataState.inbox;
  const students = _.keyBy(mailsData.users.students, 'objectId');
  const parents = _.keyBy(mailsData.users.parents, 'objectId');
  const teachers = _.keyBy(mailsData.users.teachers, 'objectId');
  const admins = _.keyBy(mailsData.users.admins, 'objectId');

  const users: UsersState = {
    students: students,
    parents: parents,
    teachers: teachers,
    admins: admins,
    currentSelectedUserProfile: INIT_USER_PROFILE
  };

  newState.dataState = {
    inbox: _.keyBy(action.payload.mails.inbox, 'objectId'),
    outbox: _.keyBy(action.payload.mails.outbox, 'objectId'),
    users: users,
    news: state.dataState.news
  };
  newState.uiState.currentUnread = getUnreadCount(action.payload.mails.inbox);
  return newState;
}

function getUnreadCount(mails: IMail[]): number {
  let unreadCount = 0;

  _.each(mails, mail => {
    if (!mail.isRead) {
      unreadCount++;
    }
  });
  return unreadCount;
}



