import { MAT_DIALOG_DATA } from '@angular/material';
import { MatDialogRef } from '@angular/material';
import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

const Parse = require('parse');

@Component({
  selector: 'sp-edit-result-dialog',
  templateUrl: './edit-result-dialog.component.html',
  styleUrls: ['./edit-result-dialog.component.css']
})
export class EditResultDialogComponent implements OnInit {

  subject = "";
  caScore = 0;
  examScore = 0;

  editForm: FormGroup;

  constructor(public editResultDialogRef: MatDialogRef<EditResultDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder) { }

  ngOnInit() {
    this.subject = this.data.get("subject").get("title");
    this.caScore = this.data.get("caScore");
    this.examScore = this.data.get("examScore");

    this.editForm = this.fb.group({
      caScore: [this.caScore],
      examScore: [this.examScore]
    });
  }

  getScores() {
    this.editResultDialogRef.close({
      id: this.data.id,
      caScore: this.editForm.value.caScore,
      examScore: this.editForm.value.examScore
    });
  }

}



