import { IRegister } from './../model/IRegister';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

const Parse = require('parse');

@Injectable()
export class ParseService {

  constructor() {
    console.log('Parse initialized!')
    Parse.initialize('9o87s1WOIyPgoTEGv0PSp9GXT1En9cwC');
    Parse.serverURL = 'https://api.schoolpop.ng/1';
  }

  public getParse() {
    return Parse;
  }

  public login(username: string, password: string): Observable<boolean> {
    console.log(username, password);
    return new Observable(observer => {
      Parse.User.logIn(username, password, {
        success: function (user) {
          observer.next(true);
        },
        error: function (user, error) {
          observer.error(error);
        }
      });
    });
  }

  public register(register: IRegister): Observable<Boolean> {
    return new Observable(observer => {
      const user = new Parse.User();
      user.set('username', register.username);
      user.set('email', register.email);
      user.set('password', register.password);
      user.set('firstName', register.firstName);
      user.set('otherName', register.otherName);
      user.set('lastName', register.lastName);
      user.set('sex', register.sex);
      user.set('city', register.city);
      user.set('state', register.state);

      user.signUp(null, {
        success: (u) => {
          observer.next(true);
          observer.complete();
        },
        error: (u, error) => {
          observer.error(error);
        }
      });
    });
  }

  public logout(): Observable<boolean> {
    return new Observable(observer => {
      Parse.User.logOut().then(() => observer.next(true), err => observer.error(err));
    });
  }

  public get currentUser() {
    return Parse.User.current();
  }

  public save(object: any) {
    return new Observable(observer => {
      object.save().then((o) => {
        observer.next(o);
        observer.complete();
      }, err => observer.error(err));
    });
  }

  changeAvatar(img: File) {
    return this.upload(img).flatMap(pf => {
      this.currentUser.set('img', pf);
      return this.save(this.currentUser);
    });
  }

  upload(file: File): Observable<File> {
    const f = new Parse.File(file.name, file, file.type);
    return new Observable(observer => {
      f.save().then(
        ff => observer.next(ff),
        err => {
          observer.error(err);
        },
        () => observer.complete()
      );
    });
  }

  public changePassword(password: string) {
    this.currentUser.set('password', password);
    return this.save(this.currentUser);
  }

}



