import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'sp-new-class-dialog',
  templateUrl: './new-class-dialog.component.html',
  styleUrls: ['./new-class-dialog.component.css']
})
export class NewClassDialogComponent implements OnInit {

  editForm: FormGroup;

  constructor(public newClassDialogRef: MatDialogRef<NewClassDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private fb: FormBuilder) { }

  ngOnInit() {
    this.editForm = this.fb.group({
      commonName: ['', Validators.required],
      fee: [0, [Validators.required, Validators.min(1)]]
    })
  }

  saveClass() {
    this.newClassDialogRef.close({
      title: this.editForm.value.commonName,
      fee: this.editForm.value.fee
    });
  }

}



