import { UserService } from './user.service';
import { ParseService } from './parse.service';
import { MomentUtil } from './../moment-util';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as moment from 'moment';
import * as _ from 'lodash';

const Parse     = require('parse');
const startDate = moment().subtract('365', 'd').toISOString();
const endDate   = moment().add('30', 'd').toISOString();

@Injectable()
export class EventService {
  school: any = this.ps.currentUser.get('school');
  role: any = this.ps.currentUser.get('role');
  constructor(private ps: ParseService, private us: UserService) { }

  getEvents(): Observable<any> {
    if (this.role === 'parent') {
      return this.getEventsParent()
      .flatMap((events: any[]) => Observable.from(events))
      .map((e: any) => {
        const event: any = {};
        event.id = e.id;
        event.title = e.get('title');
        event.url = '/events/' + e.id;
        event.start = e.get('startDate');
        event.type = e.get('type');
        event.detail = {
          text: e.get('detail').get('text'),
          venue: e.get('detail').get('venue')
        };
        if (e.get('startDate')) {
          event.end = e.get('endDate');
        } else {
          event.start = moment(e.get('startDate')).format('YYYY-MM-DD');
        }
        return event;
      })
      .toArray()
      .map(events => this.getEventsOptions(events));
    } else {
      return this.getEventsGeneral(this.school).map(events => this.getEventsOptions(events));
    }
  }

  getEventsParent(): Observable<any[]> {
    return this.us.getChildren()
      .flatMap(children => Observable.from(children))
      .map(child => child.get('school'))
      .flatMap(school => this.getEventsFromSchool(school))
      .map(events => events)
      .distinct((event: any) => event.objectId)
      .toArray()
      .map(events => events[0]);
  }

  getEventsFromSchool(school: any): Observable<any[]> {
    return new Observable<any[]>(observer => {
      const query = new Parse.Query('Event');
      query.greaterThanOrEqualTo('startDate', this.getDatePointer(startDate));
      query.include(['detail']);
      query.equalTo('school', school);
      query.find().then((evnt) => {
        observer.next(evnt);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getEventsGeneral(school: any): Observable<any[]> {
    return this.getEventsFromSchool(school)
    .map((e: any[]) => {
      const events: any[] = [];

      for (let i = 0; i < e.length; i++) {
        const event: any = {};
        event.id = e[i].id;
        event.title = e[i].get('title');
        event.url = '/events/' + e[i].id;
        event.start = e[i].get('startDate');
        event.type = e[i].get('type');
        event.detail = {
          text: e[i].get('detail').get('text'),
          venue: e[i].get('detail').get('venue')
        };
        if (e[i].get('startDate')) {
          event.end = e[i].get('startDate');
        } else {
          event.start = moment(e[i].get('startDate')).format('YYYY-MM-DD');
        }
        events.push(event);
      }
      return events;
    });
  }

  getDatePointer(date: string = moment().toISOString()) {
    return { '__type': 'Date', 'iso': date };
  }

  getEventsOptions(events: any[]): any {
    const options: any = {};
    options.aspectRatio = 2;
    options.fixedWeekCount = false;
    options.defaultView = 'listMonth';
    options.defaultDate = moment().toISOString();
    options.editable = false;
    options.eventLimit = true;
    options.events = events;
    options.theme = false;
    return options;
  }

}



