import { Observable } from 'rxjs/Observable';
import { Store } from '@ngrx/store';
import { MomentUtil } from './../../../moment-util';
import { Subscription } from 'rxjs/Rx';
import { LoadingService } from './../../../services/loading.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ParseService } from '../../../services/parse.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { adjustMainContentHeight } from '../../../app.js.helpers';
import { SearchService } from '../../../services/search.service';
import { Router } from '@angular/router';
import { MessageService } from '../../../services/message.service';
import { AppState } from '../../../message/message-state';
import { IMail } from '../../../model/IMessage';
import * as _ from 'lodash';

declare var jQuery: any;

@Component({
  selector: 'sp-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit, OnDestroy {

  mails: IMail[] = [];
  terms = '';
  isLoading: Boolean = false;

  searchForm: FormGroup;
  ps = this.parse;

  messageStreamSub: Subscription;

  moment = new MomentUtil().getMoment();

  count: number;

  mailMapping:
  { [k: string]: string } = { '=0': 'No unread mails.', '=1': 'One unread mail.', 'other': '# unread mails.' };

  constructor(private store: Store<AppState>, private ms: MessageService, private parse: ParseService, private ss: SearchService, private ls: LoadingService, private fb: FormBuilder, private router: Router) {
    store.subscribe(
      state => {
        this.mails = _.values(state.dataState.inbox);
        this.mails = this.mails.filter(mail => mail.isRead == false);
        this.count = state.uiState.currentUnread;
      }
    );
  }

  ngOnInit() {
    this.createForm();
    this.searchForm.valueChanges
      .debounceTime(500)
      .distinctUntilChanged()
      .subscribe(
      v => this.ss.setQ(v.term)
      );

    // this.messageStreamSub = this.ms.messageStream().subscribe();

    this.ls.loadingObservable.subscribe(l => this.isLoading = l)
  }

  ngOnDestroy(): void {
    // this.messageStreamSub.unsubscribe();
  }
  createForm() {
    this.searchForm = this.fb.group({
      term: ['']
    });
  }

  toggle() {
    const body = jQuery('body');
    const bodyposition = body.css('position');

    if (bodyposition !== 'relative') {

      if (!body.hasClass('sidebar-collapsed')) {
        body.addClass('sidebar-collapsed');
        jQuery('.side-navigation ul').attr('style', '');

      } else {
        body.removeClass('sidebar-collapsed chat-view');
        jQuery('.side-navigation li.active ul').css({ display: 'block' });

      }
    } else {
      if (body.hasClass('sidebar-open'))
        body.removeClass('sidebar-open');
      else
        body.addClass('sidebar-open');

      adjustMainContentHeight();
    }
  }

  getUserName(): String {
    return this.ps.currentUser.get("firstName") + " " + this.ps.currentUser.get("lastName");
  }

  logout() {
    this.ls.setLoading(true);
    this.ps.logout().subscribe(
      l => {
        this.ls.setLoading(false);
        location.href = "/";
      }
    );
  }

  getFormattedDate(date) {
    return this.moment(date).fromNow();
  }

}



