import { RouteService } from './../services/route.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ParseService } from './../services/parse.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { IParseError } from '../model/IParseError';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;
  error: string;

  loggedIn = false;
  hasError = false;
  isLoading = false;

  authForm: FormGroup;

  constructor(
    private parseService: ParseService,
    private fb: FormBuilder,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private rs: RouteService) {

    this.toastr.setRootViewContainerRef(vRef);
    this.createForm();

  }

  ngOnInit() {
    this.rs.setBc('login-body');
  }

  createForm() {
    this.authForm = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  login() {
    const username = this.authForm.value.username;
    const password = this.authForm.value.password;
    this.isLoading = true;
    this.toastr.info('Logging in.', 'Please Wait');
    this.parseService.login(username, password).subscribe(
      loggin => {
        this.loggedIn = loggin;
        this.toastr.success('Login successful.');
        location.href = '/';
      },
      err => {
        this.isLoading = false;
        console.error(err);
        this.error = err.error;
        this.toastr.error('Please check your username / password combination.', 'Login Failed');
        this.loggedIn = err.loggedIn;
      }
    );
  }

}



