import { IEvent } from './../model/IEvent';
import { RouteService } from '../services/route.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { LoadingService } from '../services/loading.service';
import { KlassService } from '../services/klass.service';
import { ParseService } from '../services/parse.service';
import { errors } from '../errors/error-messages';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-klass',
  templateUrl: './klass.component.html',
  styleUrls: ['./klass.component.css']
})
export class KlassComponent implements OnInit {

  classes: any[] = [];
  dataset: any[] = [];

  constructor(private rs: RouteService, private ps: ParseService, private ls: LoadingService,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private ks: KlassService) {

    this.toastr.setRootViewContainerRef(vRef);

  }

  ngOnInit() {
    this.rs.setBc('sticky-header');
    this.ls.setLoading(true);
    this.setup();
  }

  setup() {
    this.ks.getClasses().subscribe(
      (c: any[]) => {
        this.classes = c;
        this.ls.setLoading(false);
        this.ks.setClasses(this.classes);
        this.ks.resetDataset();
      },
      err => {
        this.ls.setLoading(false);
        console.log(errors[err.code]);
        this.toastr.error(errors[err.code]);
      }
    );
  }
}



