import { Directive, Input, AfterViewInit, Renderer, ElementRef } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import * as MD5 from './../../md5'

@Directive({
  selector: '[spGravatar]'
})
export class GravatarDirective implements AfterViewInit {
  @Input()
  email: Observable<string> = Observable.of("default@schoolpop.ng");
  
  ngOnInit() {

  }
  ngAfterViewInit(): void {
    if (this.email != null) {
      this.email.subscribe(
        email => {
          this._elRef.nativeElement.src = this.getSrc(email)
        }, err => {
          this._elRef.nativeElement.src = this.getSrc("default@schoolpop.ng")
        }
      );
    }
  }



  constructor(private _elRef: ElementRef, private _renderer: Renderer) { }

  getSrc(email):string {
    const baseUrl = 'https://www.gravatar.com/avatar/';
    const hash = MD5.encrypt(email);
    return baseUrl+hash;
  }

}



