import { Component, OnInit, OnChanges, Input, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { PagerService } from '../../../services/pager.service';

@Component({
  selector: 'sp-pager',
  templateUrl: './pager.component.html',
  styleUrls: ['./pager.component.css']
})
export class PagerComponent implements OnInit, OnChanges, AfterViewInit {

  // array of all items to be paged
  @Input()
  private allItems: any[];

  @Input()
  private perPage = 10;

  @Input()
  private totalCount = 0;

  // pager object
  pager: any = {};

  // paged items
  @Output()
  currentPage: EventEmitter<number> = new EventEmitter<number>();

  constructor(private pagerService: PagerService) { }

  ngOnInit() {
    // console.log('On Init', this.allItems);
  }

  ngAfterViewInit() {
    this.currentPage.emit(1);
  }

  ngOnChanges() {
    // console.log('On Changes', this.allItems);
    if (this.allItems && this.allItems.length > 0) {
      this.setPage(1);
    }
  }

  setPage(page: number) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }

    // get pager object from service
    this.pager = this.pagerService.getPager(this.totalCount, page);
    this.currentPage.emit(page);
  }

}



