import { STATES } from '../states.constant';
import { RouteService } from './../services/route.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ParseService } from './../services/parse.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { IRegister } from '../model/IRegister';
import { validateEmail } from '../commons/validators/validators';
import { ToastsManager } from 'ng2-toastr';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  email: String;
  registerForm: FormGroup;

  registered  = false;
  isLoading   = false;
  hasError    = false;

  states = STATES;

  constructor(
    private parseService: ParseService,
    private fb: FormBuilder,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private rs: RouteService) {

    this.toastr.setRootViewContainerRef(vRef);
    this.createForm();

  }

  ngOnInit() {
    this.rs.setBc('login-body');
  }

  createForm() {
    this.registerForm = this.fb.group({
      email: ['', [Validators.required, validateEmail]],
      username: ['', Validators.required],
      password: ['', Validators.required],
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      otherName: ['', Validators.required],
      sex: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      confirmPassword: ['', [Validators.required, this.validatePasswordConfirmation.bind(this)]]
    });
  }

  signup() {
    const account: IRegister = this.registerForm.value;
    this.isLoading = true;
    this.toastr.info('Signing up.', 'Please Wait');
    this.parseService.register(account).subscribe(
      (registered: boolean) => {
        this.registered = registered;
        this.toastr.success('Registration successful.');
        location.href = '/';

      },
      err => {
        this.toastr.error(err.message, 'Oops');
        this.isLoading = false;
        this.hasError = true;
      }
    );
  }

  validatePasswordConfirmation(control: FormControl): any {
    if (this.registerForm) {
      return control.value === this.registerForm.get('password').value ? null : { notSame: true };
    }
  }

}



