import { ISchool } from './../model/ISchool';
import { ParseService } from './parse.service';
import { UserService } from './user.service';
import { IUser } from './../model/IUser';
import { IPickupVM } from './../model/IPickupVM';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as moment from 'moment';
import * as _ from 'lodash';

const Parse = require('parse');
const Pickup = Parse.Object.extend('Pickup');

@Injectable()
export class PickupService {

  constructor(private us: UserService, private ps: ParseService) { }

  newPickup(pickup: IPickupVM, ident_pf: any): Observable<any> {
    const pk = new Pickup();
    pk.set('parent', this.us.getUserPointer(pickup.parent.objectId));
    pk.set('student', this.us.getUserPointer(pickup.student.objectId));
    pk.set('picker_firstName', pickup.firstName);
    pk.set('picker_lastName', pickup.lastName);
    pk.set('picker_ident', ident_pf);
    pk.set('done', false);
    pk.set('pickUpOn', pickup.pickUpOn);
    return this.ps.save(pk);
  }

  getPickup(student: IUser, viewAll: boolean = false): Observable<IPickupVM> {
    const query = new Parse.Query('Pickup');
    query.equalTo('student', this.us.getUserPointer(student.objectId));
    if (!viewAll) {
      query.greaterThanOrEqualTo('pickUpOn', moment(moment().format('MM/DD/YYYY'), 'MM/DD/YYYY').toDate());
      query.equalTo('done', false);
    }
    return new Observable<IPickupVM>(observer => {
      query.first().then((ps: any) => {
        // console.log(this.parseToPickup(ps));
        observer.next(this.parseToPickup(ps));
        observer.complete();
      }, err => {
        console.error(err);
        observer.error(err);
      });
    });
  }

  getPickups(parent?: IUser, school?: ISchool, viewAll: boolean = false): Observable<IPickupVM[]> {
    const query = new Parse.Query(Pickup);
    query.include(['parent', 'student', 'school']);
    if (parent) {
      query.equalTo('parent', this.us.getUserPointer(parent.objectId));
    }
    if (school) {
      query.equalTo('school', this.us.getPointer(parent.objectId, 'School'));
    }

    return new Observable<IPickupVM[]>(observer => {
      query.find().then((pu: any[]) => {
        const p: IPickupVM[] = [];
        _.each(pu, (puu) => p.push(this.parseToPickup(puu)));
        observer.next(p);
        observer.complete();
      }, err => observer.error(err));
    });
  }

  parseToPickup(p: any): IPickupVM {
    return <IPickupVM>{
      objectId: p.id,
      parent: this.us.getUserVM(p.get('parent')),
      student: this.us.getUserVM(p.get('student')),
      firstName: <string>p.get('picker_firstName'),
      lastName: <string>p.get('picker_lastName'),
      ident: <string>p.get('picker_ident').url(),
      done: <boolean>p.get('done'),
      pickUpOn: <Date>p.get('pickUpOn')
    };
  }

}



