import { RouteService } from './../services/route.service';
import { Component, OnInit } from '@angular/core';
import { ParseService } from '../services/parse.service';
import * as MD5 from './../md5';

@Component({
  selector: 'sp-not-active',
  templateUrl: './not-active.component.html',
  styleUrls: ['./not-active.component.css']
})
export class NotActiveComponent implements OnInit {

  user: any = this.ps.currentUser;

  constructor(private rs: RouteService, private ps: ParseService) { }

  ngOnInit() {
    this.rs.setBc('login-body');
  }

  getHash(email: string): string {
    const baseUrl = 'https://www.gravatar.com/avatar/';
    const hash = MD5.encrypt(email);
    return baseUrl + hash;
  }

  logout() {
    this.ps.logout().subscribe(
      stat => location.href = '/'
    );
  }

}



