import { ISchool } from './../model/ISchool';
import { ParseService } from './parse.service';
import { INews } from './../model/INews';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { UserService } from './user.service';
import * as _ from 'lodash';

const Parse = require('parse');

@Injectable()
export class NewsService {
  user = this.ps.currentUser;
  role;
  school;
  constructor(private ps: ParseService, private us: UserService) {
    if (this.user) {
      this.role = this.user.get('role');
      this.school = this.user.get('school');
    }
  }

  /*
  * TODO: Handle news serialization on server
  * Account for parents.
  */
  getNews(): Observable<INews[]> {
    const news: INews[] = [];
    return Observable.of(news);
  }
  getNewsItems(): Observable<INews[]> {
    const news: INews[] = [];
    if (this.role === 'parent') {
      return this.us.getChildrenForParents()
        .map(utm => utm.students)
        .map(children => {
          const schools: ISchool[] = [];
          _.each(children, (child) => {
            schools.push(child.school);
          });
          return schools;
        })
        .flatMap(schools => {
          const sP: any[] = [];
          _.each(schools, (school: ISchool) => {
            sP.push(this.us.getPointer(school.objectId, 'School'));
          });
          const newsQ = new Parse.Query('News');
          newsQ.containedIn('school', sP);
          return this.getNI(newsQ).debug();
        });
    } else {
      const newsQ = new Parse.Query('News');
      newsQ.equalTo('school', this.school);
      return this.getNI(newsQ).debug();
    }
  }

  getNI(query): Observable<INews[]> {
    return new Observable<INews[]>(o => {
      query.find().then((newsItem: any[]) => {
        const ns: INews[] = [];
        for (let i = 0; i < newsItem.length; i++) {
          const n = this.getNewsVM(newsItem[i]);
          ns.push(n);
        }
        o.next(ns);
        o.complete();
      }, err => {
        console.error(err);
        o.error(err);
      });
    });
  }

  getNewsVM(newsItem): INews {
    return <INews> {
      objectId: newsItem.id,
      createdAt: newsItem.get('createdAt'),
      updatedAt: newsItem.get('updatedAt'),
      title: newsItem.get('title'),
      text: newsItem.get('text'),
      img: newsItem.has('img') ? newsItem.get('img').url() : ''
    };
  }

}



