import { IMessage, IMail } from './../model/IMessage';
import { IUser } from './../model/IUser';
import { Subject, Observable } from 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { ParseService } from './parse.service';
import { EventType } from '../model/IEvent';
import { MailDB } from '../model/mail-db';
import * as _ from 'lodash';
import { UserService } from './user.service';

const Parse = require('parse');

export interface LiveMessageModel {
  eventType: number;
  item: IMail;
  currentUser: IUser;
}

@Injectable()
export class MessageService {
  // Unread mails
  private unReadMails = new Subject<any[]>();
  um = this.unReadMails.asObservable();

  public mails: any = [];
  public unreadCnt = 0;

  // Unread Count
  private unreadCount = new Subject<number>();
  uc = this.unreadCount.asObservable();

  constructor(private ps: ParseService, private us: UserService) { }


  newMessageLiveQuery(): Observable<LiveMessageModel> {
    return this.messageStream()
      .filter(lm => lm.eventType === EventType.new && lm.item.to.objectId === this.ps.currentUser.id);
  }

  messageStream(): Observable<LiveMessageModel> {

    return new Observable<LiveMessageModel>(observer => {
      const query = new Parse.Query('Mail');
      query
        .equalTo('to', this.ps.currentUser)
        .equalTo('isRead', false)
        .include(['message', 'from'])
        .descending('createdAt')
        .include(['message', 'from', 'to']);

      const subscription1 = query.subscribe();

      subscription1.on('create', (object) => {
        console.log('object created');
        observer.next({ item: this.getMail(object), eventType: EventType.new, currentUser: this.ps.currentUser });
      });

      subscription1.on('leave', (object) => {
        console.log('object left');
        observer.next({ item: this.getMail(object), eventType: EventType.edit, currentUser: this.ps.currentUser });
      });

      subscription1.on('error', (error) => {
        console.log('connection error');
        observer.error(error);
      });

      subscription1.on('close', () => {
        console.log('connection closed');
        observer.complete();
      });
    });

  }
  markAsRead(id: string): Observable<any> {
    console.log(id);
    const mail = new Parse.Object('Mail');
    mail.set('objectId', id);
    mail.set('isRead', true);
    return this.saveParseObject$(mail);
  }

  sendMail(payload: any): Observable<IMail> {

    let message: any = {};
    let user: any = {};
    const files: any[] = [];
    let mail: any = {};

    const getUser$ = this.us.getUser(payload.to);

    const createMessage$ = new Observable(observer => {
      message = new Parse.Object('Message');
      message.set('text', payload.text);

      message.save().then((m) => {
        observer.next(m);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });

    const createMail$ = (m: any, u: any) => {
      mail = new Parse.Object('Mail');
      mail.set('subject', payload.subject);
      mail.set('from', this.ps.currentUser);
      mail.set('to', u);
      mail.set('message', m);
      mail.set('isRead', false);

      return this.saveParseObject$(mail);
    };

    return getUser$.flatMap(u => {
      user = u;
      return createMessage$;
    })
    .flatMap(res => createMail$(res, user))
    .flatMap((res: any) => {
      const relation = res.relation('attachments');
      _.each(payload.attachments, attachment => {
        relation.add(attachment);
      });
      return this.saveParseObject$(res);
    })
    .map(res => this.getMail(res));
  }

  saveParseObject$(mail: any) {
    return new Observable(observer => {
      mail.save().then((m) => {
        observer.next(m);
        observer.complete();
      }, err => {
        console.log(err);
        observer.error(err);
      });
    });
  }

  uploadFiles(files: File[], mail: any): Observable<File> {
    return new Observable<File>(observer => {
      _.each(files, (file: File) => {
        const f = new Parse.File(file.name, file, file.type);
        f.save().then(
          ff => observer.next(ff),
          err => {
            console.log(err);
            observer.error(err);
          }
        );
      });
      observer.complete();
    }).map(f => f);
  }

  upload(file: File): Observable<File> {
    const f = new Parse.File(file.name, file, file.type);
    return new Observable(observer => {
      f.save().then(
        ff => observer.next(ff),
        err => {
          observer.error(err);
        },
        () => observer.complete()
      );
    });
  }

  getMailsObservable(): Observable<IMail[]> {
    return this.getInboxObservable(10, 0)
      .flatMap(mails => Observable.from(mails))
      .map(m => {
        return this.getMail(m);
      })
      .toArray();
  }

  getMailById(id: string): Observable<IMail> {
    return new Observable<IMail>(observer => {
      const query = new Parse.Query('Mail');
      query.equalTo('objectId', id);
      query.include(['from', 'to', 'message']);
      query.first().then((m) => {
        observer.next(this.getMail(m));
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getOutboxMailsObservable(): Observable<IMail[]> {
    return this.getOutboxObservable(999, 0)
      .flatMap(mails => Observable.from(mails))
      .map(m => {
        return this.getMail(m);
      })
      .toArray();
  }

  getCombinedMailObservable(): Observable<MailDB> {
    const mails: MailDB = {
      inbox: [],
      outbox: []
    };

    return this.getMailsObservable().flatMap(i => {
      mails.inbox = i;
      return this.getOutboxMailsObservable();
    }).map(o => {
      mails.outbox = o;
      return mails;
    });

  }

  setUnreadCount(c: number) {
    this.unreadCnt = c;
    this.unreadCount.next(c);
  }

  setUnreadCountError(error: Response) {
    this.unreadCount.error(error);
  }


  setUnreadMails(m: any[]) {
    this.mails = m;
    this.unReadMails.next(this.mails);
  }

  setUnreadMailsError(error: Response) {
    this.unReadMails.error(error);
  }


  getUnreadMails(limit: number, skip: number): Observable<any> {
    return new Observable(observer => {
      const query = new Parse.Query('Mail');
      query.equalTo('to', this.ps.currentUser)
        .include(['message', 'from', 'to'])
        .descending('createdAt');

      query.find().then((m) => {
        observer.next(m);
      }, (err) => {
        observer.error(err);
      });
    });
  }

  getInboxObservable(limit: number, skip: number): Observable<any[]> {
    return new Observable(observer => {
      const query = new Parse.Query('Mail');
      query.equalTo('to', this.ps.currentUser)
        .include(['message', 'from', 'to'])
        .descending('createdAt')
        .limit(limit)
        .skip(skip);

      query.find().then((m) => {
        observer.next(m);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getOutboxObservable(limit: number, skip: number): Observable<any[]> {
    return new Observable(observer => {
      const query = new Parse.Query('Mail');
      query.equalTo('from', this.ps.currentUser)
        .include(['message', 'from', 'to'])
        .descending('createdAt')
        .limit(limit)
        .skip(skip);

      query.find().then((m) => {
        observer.next(m);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }



  getMail(m: any): IMail {
    const mail: IMail = <IMail>{
      objectId: m.id,
      from: this.us.getUserVM(m.get('from')),
      to: this.us.getUserVM(m.get('to')),
      message: this.getMessage(m.get('message')),
      isRead: <boolean>m.get('isRead'),
      subject: <string>m.get('subject'),
      createdAt: m.get('createdAt'),
      updatedAt: m.get('updatedAt')
    };
    return mail;
  }
  getMessage(message: any): IMessage {
    return  {
      objectId: message.id,
      text: message.get('text'),
      createdAt: message.get('createdAt'),
      updatedAt: message.get('updatedAt')
    };
  }

}




