import { errors } from './../../errors/error-messages';
import { ToastsManager } from 'ng2-toastr';
import { MatDialog } from '@angular/material';
import { EventType } from './../../model/IEvent';
import { LoadingService } from './../../services/loading.service';
import { KlassService } from './../../services/klass.service';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ParseService } from '../../services/parse.service';
import { RouteService } from '../../services/route.service';
import { IEvent } from '../../model/IEvent';
import { Router } from '@angular/router';
import { NewClassDialogComponent } from '../../commons/widget/new-class-dialog/new-class-dialog.component';

@Component({
  selector: 'sp-klass-list',
  templateUrl: './klass-list.component.html',
  styleUrls: ['./klass-list.component.css']
})
export class KlassListComponent implements OnInit {

  classes: any[] = [];
  dataset: any[] = [];
  editable: Boolean = this.ps.currentUser.get("role") == "admin";
  isLoading = false;

  constructor(private rs: RouteService, private router: Router, private dialog: MatDialog,
    private toastr: ToastsManager,
    vRef: ViewContainerRef,
    private ps: ParseService, private ls: LoadingService, private ks: KlassService) {

    this.toastr.setRootViewContainerRef(vRef);

  }

  ngOnInit() {
    this.rs.setBc("sticky-header");

    this.ks.getDataSetObservable.subscribe(
      l => {
        this.dataset = l;
        this.isLoading = false;
      },
      err => {
        this.isLoading = false;
        console.log(err);
      }
    );
  }

  doEvent(e: IEvent) {
    let c = Object.assign([], this.ks.classes);
    let item = c.filter(i => i.id == e.objectId)[0];

    if (e.event == EventType.view) {
      this.router.navigate(['class', item.id]);
    } else if (e.event == EventType.delete) {
      this.isLoading = true;
      this.ks.deleteClass(item).subscribe(
        c => this.ks.resetDataset(c)
      );
    }
  }

  public newClass() {
    let newClassDialogRef = this.dialog.open(NewClassDialogComponent);
    newClassDialogRef.afterClosed()
      .filter(r => {
        if (r !== undefined && r !== "cancel") {
          return r;
        }
        return r !== undefined && r !== "cancel";
      })
      .flatMap(t => {
        this.isLoading = true;
        return this.ks.newClass(t.title, parseInt(t.fee));
      })
      .subscribe(
      c => {
        this.isLoading = false;
        this.ks.addClass(c);
        console.log(c);
      },
      err => {
        this.isLoading = false;
        console.log(err);
        this.toastr.error(errors[err.code]);
      }
      );
  }

}



