import { DashboardData, INIT_DASHBOARD_DATA } from './../model/user-transfer-model';
import { LiveMessageModel } from './../services/message.service';
import { IMail } from './../model/IMessage';
import { Action } from "@ngrx/store";
import { MailDB } from '../model/mail-db';

export const INIT_ACTION = 'LOAD_MAIL_ACTION';
export const LOAD_VIEW_MESSAGE_ACTION = 'LOAD_VIEW_MESSAGE_ACTION';
export const MAIL_LOADED_ACTION = 'MAIL_LOADED_ACTION';
export const VIEW_MESSAGE_LOADED_ACTION = 'VIEW_MESSAGE_LOADED_ACTION';
export const CHANGE_CURRENT_MESSAGE_SECTION_ACTION = 'CHANGE_CURRENT_MESSAGE_SECTION_ACTION';
export const SEND_MESSAGE_ACTION = 'SEND_MESSAGE_ACTION';
export const MESSAGE_SENT_ACTION = 'MESSAGE_SENT_ACTION';

export class ChangeCurrentMessageSection implements Action {
  readonly type: string = CHANGE_CURRENT_MESSAGE_SECTION_ACTION;
  constructor(public payload?: number) {}

}

export class SendMessageAction implements Action {
  readonly type: string = SEND_MESSAGE_ACTION;
  constructor(public payload?: any) {}
}

export class MessageSentAction implements Action {
  readonly type: string = MESSAGE_SENT_ACTION;
  constructor(public payload?: IMail) {
  }
}

export class MailLoadedAction implements Action {
  readonly type: string = MAIL_LOADED_ACTION;
  constructor(public payload?: DashboardData) {
  }
}

export class LoadDashBoardAction implements Action {
  readonly type: string = INIT_ACTION;
}

export class LoadedViewMessageAction implements Action {
  readonly type: string = VIEW_MESSAGE_LOADED_ACTION;

	// Payload is id of message to be viewed
  constructor(public payload?: any) {}
}

export class LoadViewMessageAction implements Action {
  readonly type: string = LOAD_VIEW_MESSAGE_ACTION;

	// Payload is id of message to be viewed
  constructor(public payload?: any) {}
}

export const MARK_AS_READ_ACTION = 'MARK_AS_READ_ACTION';

export class MarkAsReadAction implements Action {
  readonly type: string = MARK_AS_READ_ACTION;

  constructor(public payload: string) {}
}

export const STREAM_NEW_MESSAGE_ACTION = 'STREAM_NEW_MESSAGE_ACTION';
export class StreamNewMessageAction implements Action {
  readonly type: string = STREAM_NEW_MESSAGE_ACTION;

  constructor(public payload: LiveMessageModel) {}
}




