import { DirectivesModule } from './../commons/directives/directives.module';
import { ParseService } from './../services/parse.service';
import { RouteService } from './../services/route.service';
import { LoadingService } from './../services/loading.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SettingsComponent } from './settings.component';
import { RouterModule } from '@angular/router';
import { MatCardModule, MatIconModule, MatMenuModule, MatDialogModule, MatProgressBarModule, MatIconRegistry, MatInputModule, MatButtonModule, MatSelectModule } from '@angular/material';
import { WidgetModule } from '../commons/widget/widget.module';
import { UserService } from '../services/user.service';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TextEqualityValidatorModule } from 'ngx-text-equality-validator';
import { ToastModule, ToastOptions } from 'ng2-toastr';
import { CustomOption } from '../toastr.options';

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    DirectivesModule,
    ReactiveFormsModule,
    RouterModule,
    MatButtonModule,
    MatCardModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatDialogModule,
    MatProgressBarModule,
    MatSelectModule,
    TextEqualityValidatorModule,
    ToastModule.forRoot(),
    WidgetModule
  ],
  declarations: [SettingsComponent],
  providers: [
    LoadingService,
    RouteService,
    ParseService,
    UserService,
    MatIconRegistry,
    { provide: ToastOptions, useClass: CustomOption }
  ]
})
export class SettingsModule { }



