import { UserService } from './../../services/user.service';
import { IUser } from './../../model/IUser';
import { Observable } from 'rxjs/Observable';
import { NG_ASYNC_VALIDATORS, Validator, AbstractControl } from '@angular/forms';
import { Directive, forwardRef } from '@angular/core';

@Directive({
  selector: '[asyncValidator][formControlName], [asyncValidator][ngModel]',
  providers: [
    {
      provide: NG_ASYNC_VALIDATORS,
      useExisting: forwardRef(() => AsyncValidator), multi: true
    },
    UserService
  ]
})
export class AsyncValidator implements Validator {

  constructor(private us:UserService) { }

  validate(c: AbstractControl): Promise<{ [key: string]: any }> | Observable<{ [key: string]: any }> {
    return validateUserExistByUsernameEmailObjectId(c.value).first();
  }

}

const validateUserExistByUsernameEmailObjectId = (term: string): Observable<any> => {
  return this.us.searchUserByTerm(term).map((u: any[]) => {
    if (u.length == 0) {
      return { asyncInvalid: true }
    } else {
      return null;
    }
  });
}



