import { ReportService } from '../../report.service';
import { FETCH_REPORT_ACTION } from './../../../actions/report-actions';
import { Injectable } from '@angular/core';
import { Actions, Effect } from '@ngrx/effects';
import { ReportsFetchedAction } from '../../../actions/report-actions';

@Injectable()
export class ReportEffectService {

  @Effect() getReports = this.actions$
    .ofType(FETCH_REPORT_ACTION)
    .switchMap(action => this.reportService.getReports(action.payload))
    .map(reports => new ReportsFetchedAction(reports));

  constructor(private reportService: ReportService, private actions$: Actions) { }
}



