import { IEvent, EventType } from './../../../model/IEvent';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
// import { DataSource } from '@angular/cdk';

@Component({
  selector: 'sp-result-list-widget',
  templateUrl: './result-list-widget.component.html',
  styleUrls: ['./result-list-widget.component.scss']
})
export class ResultListWidgetComponent implements OnInit {

  @Input()
  reports: any[] = [];

  @Input()
  user: any;

  @Input()
  editable: Boolean = false;

  @Output()
  event: EventEmitter<IEvent> = new EventEmitter<IEvent>();

  @Output()
  onRefresh: EventEmitter<Boolean> = new EventEmitter<Boolean>();

  dataset: any[] = [];

  rerender = false;

  constructor(private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.createDataset();
  }

  createDataset() {
    for (let index = 0; index < this.reports.length; index++) {
      const element = this.reports[index];
      const item: any = {};
      item.id = index + 1;
      item.objectId = element.id;
      item.score = element.get('score');
      item.withExam = element.get('withExam');
      item.caScore = element.get('caScore');
      item.examScore = element.get('examScore');
      item.subject = element.get('subject').get('title');
      item.remark = this.getRemark(element.get('score'), item.withExam);
      this.dataset.push(item);
    }
  }

  getRemark(score: number, we: Boolean): String {
    if (score < 45 && we) {
      return 'Failed';
    } else if (score > 45) {
      return 'Passed';
    } else {
      return 'No Remarks';
    }
  }

  emitEvent(event: IEvent) {
    // TODO: Handle event locally before emitting
    if (event.event === EventType.delete) {
      this.dataset = this.dataset.filter(item => item.objectId !== event.objectId);
    }
    this.event.emit(event);
  }

}



