import { LoadingService } from './../services/loading.service';
import { EventService } from '../services/event.service';
import { Component, OnInit } from '@angular/core';
import { RouteService } from '../services/route.service';
import { Observable } from 'rxjs/Rx';
import { ParseService } from '../services/parse.service';

@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.css']
})
export class EventComponent implements OnInit {

  events$: Observable<any[]>;
  eventsFiltered$: Observable<any[]>;

  user: any = this.ps.currentUser;

  constructor(private ls: LoadingService, private rs: RouteService, private ps: ParseService, private evntS: EventService) { }

  ngOnInit() {
    this.rs.setBc('sticky-header');
    this.ls.setLoading(true);
    this.events$ = this.evntS.getEvents().map(e => e.events).do(() => this.ls.setLoading(false));
  }

  getTypeColor(type: number): string {
    switch (type) {
      case 0:
        return 'purple';
      case 1:
        return 'green';
      case 2:
        return 'yellow';
      default :
        return 'purple';
    }
  }
  getTypeTitle(type: number): string {
    switch (type) {
      case 0:
        return 'General Meeting';
      case 1:
        return 'Open Day';
      case 2:
        return 'Party';
      default :
        return 'purple';
    }
  }

}



