import { INews } from './../../../model/INews';
import { Component, HostBinding, OnInit, ElementRef, AfterViewInit, Input, OnDestroy } from '@angular/core';

declare var $: any, jQuery: any;
import 'owl.carousel';
import { NewsService } from '../../../services/news.service';

@Component({
  selector: 'news-widget',
  templateUrl: './news-widget.component.html',
  styleUrls: ['./news-widget.component.css']
})
export class NewsWidgetComponent implements AfterViewInit, OnDestroy {

  @HostBinding('class') defaultClass = 'owl-carousel';
  @Input()
  news: INews[] = [];
  $owlElement: any;

  constructor(private el: ElementRef) { }

  ngAfterViewInit(): void {

    this.$owlElement = $(this.el.nativeElement).owlCarousel({
      loop: true,
      center: true,
      items: 1,
      margin: 10,
      nav: false,
      autoplay: true,
      autoplayTimeout: 1000 * 10,
      autoplayHoverPause: true,
      autoHeight: true
    });
  }

  ngOnDestroy() {
    this.$owlElement.data('owlCarousel').destroy();
    this.$owlElement = null;
  }

}



