import { ChartDataset } from './../../../model/IUser';
import { Component, OnInit, Input, ViewChild, AfterViewChecked, OnChanges, SimpleChanges } from '@angular/core';
import { BaseChartDirective } from 'ng2-charts';

@Component({
  selector: 'sp-chart-widget',
  templateUrl: './chart-widget.component.html',
  styleUrls: ['./chart-widget.component.css']
})
export class ChartWidgetComponent implements OnInit, AfterViewChecked, OnChanges {

  @ViewChild( 'chartName' ) myChart: BaseChartDirective;

  @Input()
  chartType: string;

  @Input()
  dataset: ChartDataset = {
    labels : [],
    data : []
  };

  @Input()
  options: any = {};

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewChecked(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.myChart.ngOnChanges({});
  }

  public chartClicked(e: any): void {
    console.log(e);
  }

  public chartHovered(e: any): void {
    console.log(e);
  }

}



