import { IReport } from './../../model/IReport';
import { IUser } from './../../model/IUser';
import { IPickupVM } from './../../model/IPickupVM';

export interface ProfileState {
  pickupState: PickupState;
  currentSelectedUser: IUser;
  currentSelectedUserResults: IReport[];
}

export interface PickupState {
  pickup: IPickupVM;
  pickups: { [key: string]: IPickupVM};
  dirty: boolean;
}

export const INIT_PICKUP_STATE: PickupState = {
  pickup: undefined,
  dirty: undefined,
  pickups: {}
};

export const  INIT_PROFILE_STATE: ProfileState = {
  pickupState: INIT_PICKUP_STATE,
  currentSelectedUser: undefined,
  currentSelectedUserResults: []
};



