import { MessageService } from './../../message.service';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Actions, Effect } from '@ngrx/effects'
import { Observable } from 'rxjs/Rx';
import { LoadedViewMessageAction, LOAD_VIEW_MESSAGE_ACTION, MARK_AS_READ_ACTION } from '../../../message/message-action';

@Injectable()
export class ViewMailServiceEffect {
  @Effect() viewMessage$ = this.actions$
    .ofType(LOAD_VIEW_MESSAGE_ACTION)
    .map(action => new LoadedViewMessageAction(action.payload));

  @Effect({dispatch: false}) markAsRead$ = this.actions$
    .ofType(MARK_AS_READ_ACTION)
    .switchMap(action => this.ms.markAsRead(action.payload));

  constructor(private actions$: Actions, private ms:MessageService) { }


}



