import { ParseService } from '../../services/parse.service';
import { errors } from './../../errors/error-messages';
import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IEvent, EventType } from '../../model/IEvent';
import { KlassService } from '../../services/klass.service';
import { ToastsManager } from 'ng2-toastr';
import { MatDialog } from '@angular/material';
import { AssignTeacherDialogComponent } from '../../commons/widget/assign-teacher-dialog/assign-teacher-dialog.component';
import { LoadingService } from '../../services/loading.service';
import { ComposeDialogComponent } from '../../commons/widget/compose-dialog/compose-dialog.component';

@Component({
  selector: 'sp-klass-view',
  templateUrl: './klass-view.component.html',
  styleUrls: ['./klass-view.component.css']
})
export class KlassViewComponent implements OnInit {

  klass: any = {};

  isLoading = false;

  editable = false;

  hasTeacher = false;

  isAdmin: boolean = this.ps.currentUser.get('role') === 'admin';
  isTeacher: boolean = this.ps.currentUser.get('role') === 'teacher';

  dataset: any[] = [];

  constructor(private route: ActivatedRoute, private router: Router,
    private ks: KlassService,
    private dialog: MatDialog,
    private ls: LoadingService,
    private toastr: ToastsManager,
    private ps: ParseService,
    vRef: ViewContainerRef) {

      this.toastr.setRootViewContainerRef(vRef);

    }

  ngOnInit() {
    this.klass = this.route.snapshot.data['class'];
    this.hasTeacher = this.klass.has('teacher');
    this.getStudents(this.klass);
  }

  getStudents(clazz: any) {
    this.isLoading = true;
    const students: string[] = clazz.get('students');
    this.ks.getStudents(students).subscribe(
      s => {
        this.isLoading = false;
        this.dataset = s;
      },
      err => {
        this.isLoading = false;
        this.toastr.error(errors[err.code]);
      },
      () => {
        console.log('Completed');
      }
    );
  }

  doEvent(e: IEvent) {
    if (e.event === EventType.view) {
      this.router.navigate(['user', e.objectId]);
    }
  }

  sendMessageToClass() {
    const composeDialogRef = this.dialog.open(ComposeDialogComponent, {
      height: '400px',
      width: '600px'
    });
  }

  assignTeacher() {
    const assignTeacherDialogRef = this.dialog.open(AssignTeacherDialogComponent, {
      height: '400px',
      width: '600px'
    });

    assignTeacherDialogRef.afterClosed().filter(t => t !== undefined).subscribe(t => {
      this.ls.setLoading(true);
      console.log(t);
      this.ks.setTeacher(this.klass, t.teacher).subscribe(
        c => {
          this.klass = c;
          this.hasTeacher = true;
        },
        err => {
          this.ls.setLoading(false);
          console.log(err);
        },
        () => this.ls.setLoading(false)
      );
    });
  }

}



