import { Subscription, Observable } from 'rxjs/Rx';
import { Component, OnInit, Input, EventEmitter, Output, OnDestroy } from '@angular/core';
import { MatIconRegistry } from '@angular/material';
import { DomSanitizer } from '@angular/platform-browser';

const Parse = require('parse');

@Component({
  selector: 'sp-file-uploader',
  templateUrl: './file-uploader.component.html',
  styleUrls: ['./file-uploader.component.css']
})
export class FileUploaderComponent implements OnInit, OnDestroy {

  @Input()
  file:FileUploadModel;

  @Output()
  onFileUploaded:EventEmitter<any> = new EventEmitter<any>();

  uploadSub:Subscription;

  constructor(iconRegistry: MatIconRegistry, sanitizer: DomSanitizer) {
    iconRegistry.addSvgIcon(
      'png',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/png.svg'));

    iconRegistry.addSvgIcon(
      'file',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/file.svg'));

    iconRegistry.addSvgIcon(
      'svg',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/svg.svg'));

    iconRegistry.addSvgIcon(
      'pdf',
      sanitizer.bypassSecurityTrustResourceUrl('assets/img/svg/pdf.svg'));
  }

  ngOnInit() {
    if (this.file.isDirty) {
      this.uploadFile(this.file.file).subscribe(
        att => {
          this.file.isDirty = false;
          this.onFileUploaded.emit({pf:att, local:this.file});
        },
        err => {
          console.log(err);
        }
      );
    }
  }

  ngOnDestroy() {
    if (this.uploadSub) {
      this.uploadSub.unsubscribe();
    }
  }

  uploadFile(file:File):Observable<any> {
    let upload = new Observable(observer => {
      let f = new Parse.File(file.name, file, file.type);
      f.save().then((pf) => {
        let attachment = new Parse.Object("Attachment");
        attachment.set("file", pf);
        return attachment.save();
      }).then((att) => {
        observer.next(att);
        observer.complete()
      }, err => {
        observer.error(err);
      });
    });
    return upload;
  }

  getIcon(file: File): string {
    if (file.type.match('image/*')) {
      return 'png';
    } else if (file.type === 'application/pdf') {
      return 'pdf';
    }
    switch (file.type) {
      case 'image/svg+xml':
        return 'svg';
      default:
        return 'file';
    }
  }

}

export interface FileUploadModel {
  file: File,
  isDirty:boolean
}



