import { Subject } from 'rxjs/Rx';
import { ParseService } from './parse.service';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { UserService } from './user.service';

const Parse = require('parse');
const ClassRoom = Parse.Object.extend('ClassRoom');

@Injectable()
export class KlassService {


  classes: any[] = [];
  dataset: any[] = [];


  datasetSubject: Subject<any[]> = new Subject<any[]>();

  constructor(private ps: ParseService, private us: UserService) { }

  setTeacher(clazz: any, teacher: any) {
    return new Observable(observer => {
      clazz.set('teacher', teacher);

      clazz.save().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  studentInClass(id: string): Observable<boolean> {
    return this.getClassByStudent(id).map(c => c !== null);
  }

  getStudents(students: string[]) {

    const o: Observable<string> = new Observable(observer => {
      students.map(s => {
        observer.next(s);
      });
      observer.complete();
    });

    return o.flatMap(s => this.us.getStudent(s)).map(u => this.us.getUserItem(u)).toArray();

    // return Observable.of(students).map((s:String[], i:number) => {
    //   return s[i];
    // }).flatMap(s => this.us.getStudent(s)).toArray();
  }

  getClassByStudent(id: string): Observable<any> {
    const query = new Parse.Query(ClassRoom);
    query.equalTo('students', id);
    return new Observable<any>(observer => {
      query.first().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getClass(id) {
    return new Observable(observer => {
      const query = new Parse.Query(ClassRoom);
      query.equalTo('objectId', id);
      query.include(['teacher']);
      query.first().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  newClass(title: String, fee?:number) {
    let cl;
    return this.createClass(title, fee).flatMap(c => {
      cl = c;
      return this.saveClassToSchool(cl, this.ps.currentUser.get("school"));
    }).map((s) => cl);
  }

  createClass(title: String, fee: number) {
    return new Observable(observer => {
      let classRoom = new ClassRoom();

      classRoom.set("commonName", title);
      classRoom.set("students", []);
      classRoom.set("fee", fee)
      classRoom.save().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  saveClassToSchool(c, s) {
    return new Observable(observer => {
      var relation = s.relation("classRoom");
      relation.add(c);
      s.save().then((s) => {
        observer.next(s);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getClasses() {
    let school = this.ps.currentUser.get("school");

    return new Observable(observer => {
      var relation = school.relation("classRoom");
      var query = relation.query();
      query.ascending("commonName");

      query.find().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  deleteClass(c: any) {
    return new Observable(observer => {
      c.destroy().then((cl) => {
        observer.next(cl);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  setDataset(ds: any[]) {
    this.dataset = ds;
    this.datasetSubject.next(this.dataset);
  }

  setClasses(classes: any[]) {
    this.classes = classes;
  }

  resetDataset(c?: any) {
    this.dataset = [];
    if (c !== undefined) {
      this.classes = this.classes.filter(i => i.id !== c.id);
    }
    for (var index = 0; index < this.classes.length; index++) {
      var element = this.classes[index];
      let item: any = {};
      item = this.getItem(index + 1, element);
      this.dataset.push(item);
    }
    this.datasetSubject.next(this.dataset);
  }

  addClass(c) {
    let item: any = {};
    this.classes.push(c);
    this.resetDataset();
  }

  getItem(i: number, element: any) {
    let item: any = {};
    item.id = i;
    item.objectId = element.id;
    item.commonName = element.get("commonName");
    item.sCount = element.get("students").length;

    return item;
  }

  getDataSetObservable = this.datasetSubject.asObservable();

}



