import { SettingsComponent } from './settings/settings.component';
import { NotActiveComponent } from './not-active/not-active.component';
import { OutboxComponent } from './message/outbox/outbox.component';
import { InboxComponent } from './message/inbox/inbox.component';
import { SchoolResolver } from './resolvers/school.resolver';
import { MainConfigComponent } from './school/main-config/main-config.component';
import { KlassViewComponent } from './klass/klass-view/klass-view.component';
import { BasicComponent } from './commons/layout/basic/basic.component';
import { BlankComponent } from './commons/layout/blank/blank.component';
import { ComposePopUpComponent } from './message/compose-pop-up/compose-pop-up.component';
import { ComposeComponent } from './message/compose/compose.component';
import { AttendanceComponent } from './user/attendance/attendance.component';
import { ProfileComponent } from './profile/profile.component';
import { UserNewComponent } from './user/new/new.component';
import { UsersListComponent } from './user/list/list.component';
import { ListProfileComponent } from './user/profile/profile.component';
import { RedirectStudentGuard } from './guards/redirect-student.guard';
import { LoginComponent } from './login/login.component';
import { Routes } from '@angular/router';
import { KlassComponent } from './klass/klass.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { FiveOhXComponent } from './errors/five-oh-x/five-oh-x.component';
import { FourOhFourComponent } from './errors/four-oh-four/four-oh-four.component';
import { EventComponent } from './event/event.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { MessageComponent } from './message/message.component';
import { NewsComponent } from './news/news.component';
import { RegisterComponent } from './register/register.component';
import { UserComponent } from './user/user.component';
import { AuthenticatedGuard } from './guards/authenticated.guard';
import { UserResolver } from './resolvers/user';
import { RComponent } from './user/r/r.component';
import { KlassListComponent } from './klass/klass-list/klass-list.component';
import { KlassResolver } from './resolvers/klass.resolver';
import { SchoolComponent } from './school/school.component';
import { AddAdminComponent } from './school/add-admin/add-admin.component';
import { ViewMessageComponent } from './message/view/view.component';
import { IsActivatedGuard } from './guards/is-activated.guard';
import { AddTeacherComponent } from './school/add-teacher/add-teacher.component';
import { TermComponent } from './school/term/term.component';
export const ROUTES: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  {
    path: 'dashboard', loadChildren: './dashboard/dashboard.module#DashboardModule'
  },
  {
    path: 'profile',
    loadChildren: './profile/profile.module#ProfileModule'
  },
  {
    path: 'event',
    component: BasicComponent,
    children: [
      {
        path: '',
        component: EventComponent
      }
    ]
  },
  {
    path: 'class',
    component: BasicComponent,
    children: [
      {
        path: '',
        component: KlassComponent,
        canActivate: [
          AuthenticatedGuard
        ],
        children: [
          {
            path: '',
            component: KlassListComponent
          },
          {
            path: ':id',
            component: KlassViewComponent,
            resolve: {
              class: KlassResolver
            }
          }
        ]
      }
    ]
  }, {
    path: 'forgot-password',
    component: ForgotPasswordComponent
  },
  {
    path: 'message',
    loadChildren: './message/message.module#MessageModule'
  },
  {
    path: 'news',
    loadChildren: './news/news.module#NewsModule'
  },
  {
    path: 'parents',
    loadChildren: './parents/parents.module#ParentsModule'
  },
  {
    path: 'setting',
    children: [
      {
        path: '',
        component: SettingsComponent
      }
    ],
    component: BasicComponent
  },
  {
    path: 'school',
    loadChildren: './school/school.module#SchoolModule'
  },
  {
    path: 'user',
    loadChildren: './user/user.module#UserModule'
  },
  {
    path: '',
    component: BlankComponent,
    children: [
      {
        path: '404',
        component: FourOhFourComponent
      },
      {
        path: '50x',
        component: FiveOhXComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'inactive',
        component: NotActiveComponent
      },
      {
        path: 'register',
        component: RegisterComponent
      }
    ]
  }
  ,
  { path: '**', redirectTo: '/404' }
];



