import { INews } from './../../../model/INews';
import { Component, OnInit, Input, ElementRef, ViewChild, Renderer, AfterViewInit } from '@angular/core';

declare var jQuery: any;

@Component({
  selector: 'sp-news-card',
  templateUrl: './news-card.component.html',
  styleUrls: ['./news-card.component.css']
})
export class NewsCardComponent implements OnInit, AfterViewInit {

  @Input()
  news: INews;

  @ViewChild('card') cardElement: ElementRef;

  constructor(private renderer: Renderer) { }

  ngAfterViewInit(): void {
    if (this.cardElement && this.news && this.news.img) {
      const new_css = {
        'background-image': `url("${this.news.img}")`,
        'background-position': 'center center',
        'background-size': 'cover'
      };
      const ne = jQuery(this.cardElement.nativeElement);
      ne.css(new_css);
    }
  }
  ngOnInit() {}

}



