import { AppState } from './../message/message-state';
import { IPickupVM } from './../model/IPickupVM';
import { getSrc } from '../md5';
import { IImg } from './../model/IImg';
import { UserTransferModel, INIT_USER_TRANSFER } from './../model/user-transfer-model';
import { IUser } from './../model/IUser';
import { Observable } from 'rxjs/Observable';
import { skip } from 'rxjs/operator/skip';
import { Injectable } from '@angular/core';
import { ParseService } from './parse.service';
import { Subject } from 'rxjs/Rx';
import { FormControl } from '@angular/forms';
import * as _ from 'lodash';
import { KlassService } from './klass.service';
import { Store, Action } from '@ngrx/store';
import { PickupService } from './pickup.service';

const Parse = require('parse');
const ClassRoom = Parse.Object.extend('ClassRoom');
const Pickup = Parse.Object.extend('Pickup');

@Injectable()
export class UserService {

  studentSubject: Subject<any> = new Subject<any>();
  studentDatasetObservable = this.studentSubject.asObservable();
  currentUser: any;

  constructor(
    private ps: ParseService,
    private store: Store<AppState>) { }

  linkParent(parent: IUser, profile: any): Observable<IUser> {
    profile.user.set('parent', this.getUserPointer(parent.objectId));
    return this.ps.save(profile.user).map(p => parent);
  }

  setStudentsDataset(students: any[]) {
    this.studentSubject.next(students);
  }

  setStudent(student: any) {
    this.studentSubject.next(student);
  }

  getUserCount(role: string): Observable<number> {
    return new Observable<number>(o => {
      Parse.Cloud.run('userCount', {'role': role}).then((count) => {
        o.next(count);
        o.complete();
      }, err => o.error(err));
    });
  }

  getCurrentUser(): any {
    return this.currentUser;
  }

  setCurrentUser(user: any) {
    this.currentUser = user;
  }

  getUsersByRole(school: any = this.ps.currentUser.get('school'), role: string = 'user', limit = 10, skip = 0) {
    const users: any = {};
    const query = new Parse.Query(Parse.User);
    query.equalTo('school', school);
    query.equalTo('role', role);
    query.include(['school']);
    query.ascending('lastName');
    query.ascending('firstName');

    return new Observable(observer => {
      query.count().then((c) => {
        users.count = c;
        query.limit(limit);
        return query.find();
      }).then((u) => {
        users.users = u;
        observer.next(users);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getStudentsCountBySchool(school: any = this.ps.currentUser.get('school')) {
    return new Observable(observer => {
      const User = Parse.Object.extend(Parse.User);
      const query = new Parse.Query(User);
      query.equalTo('school', school);
      query.equalTo('role', 'user');
      query.count().then((c) => {
        observer.next(c);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }


  getStudentsBySchool(limit: number = 10, skip: number = 0, school: any = this.ps.currentUser.get('school')) {
    return new Observable(observer => {
      const User = Parse.Object.extend(Parse.User);
      const query = new Parse.Query(User);
      query.equalTo('school', school);
      query.equalTo('role', 'user');
      query.include(['school']);
      query.ascending('lastName');
      query.ascending('firstName');
      query.limit(limit);
      query.find().then((s) => {
        observer.next(s);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getChildren(limit: number = 10, skip: number = 0, user: any = this.ps.currentUser): Observable<any[]> {
    return new Observable(observer => {
      const Profile = Parse.Object.extend('Profile');
      const query = new Parse.Query(Profile);
      query.equalTo('parent', user);
      query.include(['user', 'parent']);
      query.descending('createdAt');
      query.find().then((users) => {
        const children = [];
        _.each(users, (u: any) => children.push(u.get('user')));
        observer.next(children);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getStudentsFromSchool(schoolId?: String, limit: number = 10, skip: number = 0, user: any = this.ps.currentUser): Observable<any[]> {
    return new Observable();
  }

  getStudent(id: String): Observable<any> {
    return new Observable(observer => {
      const query = new Parse.Query(Parse.User);
      query.equalTo('objectId', id);
      query.include(['profile', 'school']);
      query.first().then((s) => {
        observer.next(s);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getParents(schoolId?: String, limit: number = 10, skip: number = 0, user: any = this.ps.currentUser): Observable<any[]> {
    return new Observable();
  }

  getStaff(schoolId?: String, limit: number = 10, skip: number = 0, user: any = this.ps.currentUser): Observable<any[]> {
    return new Observable();
  }

  getUserProfile(id: String) {
    return new Observable(observer => {
      const User = Parse.Object.extend('Profile');
      const query = new Parse.Query(User);
      query.equalTo('user', this.getUserPointer(id));
      query.include(['parent', 'user', 'user.school']);
      query.first().then((u) => {
        observer.next(u);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getResultObservable(user: any, term?: any) {
    return new Observable(observer => {
      const query = new Parse.Query('Report');
      query.equalTo('student', user);
      if (term) {
        query.equalTo('term', term);
      }
      query.first().then((rep) => {
        const relation = rep.relation('subjects');
        const q = relation.query();
        q.include('subject');
        q.find().then((s) => {
          observer.next(s);
          observer.complete();
        });
      }, err => {
        observer.error(err);
      });
    });
  }

  getAttendanceObservable(user: any) {
    return new Observable(observer => {
      const query = new Parse.Query('Activity');
      query.equalTo('type', 'attendance');
      query.equalTo('fromUser', user);
      query.include(['markedBy']);
      query.find().then((a) => {
        observer.next(a);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getProfile(id: String): Observable<any> {
    const profile: any = {};
    profile.user = {};
    profile.reports = [];
    profile.attendance = [];

    const userObs: Observable<any> = this.getUserProfile(id);

    return userObs.flatMap((user) => {
      profile.user = user;
      return this.getResultObservable(user.get('user'));
    }).flatMap((results) => {
      profile.reports = results;
      return this.getAttendanceObservable(profile.user.get('user'));
    }).map((att) => {
      profile.attendance = att;
      return profile;
    });
  }

  deleteResult(rid: String) {
    return this.getResultById(rid).flatMap((o) => this.deleteObject(o));
  }

  getResultById(id) {
    return new Observable(observer => {
      const query = new Parse.Query('Subject');
      query.equalTo('objectId', id);
      query.first().then((o) => {
        observer.next(o);
      }, err => {
        observer.error(err);
      });
    });
  }

  deleteObject(o) {
    return new Observable(observer => {
      o.destroy().then((object) => {
        observer.next(object);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  saveReport(report: any) {
    return new Observable(observer => {
      report.save().then((r) => {
        observer.next(r);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  searchUserByTerm(term: string, role?, school?) {
    const usernameQuery = new Parse.Query(Parse.User);
    usernameQuery.startsWith('p_username', term.toLowerCase());

    const emailQuery = new Parse.Query(Parse.User);
    emailQuery.startsWith('p_email', term.toLowerCase());

    const firstNameQuery = new Parse.Query(Parse.User);
    firstNameQuery.startsWith('p_firstName', term.toLowerCase());

    const lastNameQuery = new Parse.Query(Parse.User);
    lastNameQuery.startsWith('p_lastName', term.toLowerCase());

    const idQuery = new Parse.Query(Parse.User);
    lastNameQuery.equalTo('objectId', term);

    const mainQuery = Parse.Query.or(usernameQuery, emailQuery, firstNameQuery, lastNameQuery, idQuery);
    if (role !== undefined) {
      mainQuery.equalTo('role', role);
    }
    if (school !== undefined) {
      mainQuery.equalTo('school', school);
    }

    return new Observable(observer => {
      mainQuery.find().then((t) => {
        observer.next(t);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });

  }

  getFee(sid: string) {
    return new Observable(observer => {
      Parse.Cloud.run('getFees', { sid: sid }).then((res) => {
        observer.next(res);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getUserPointer(id: String) {
    return {
      '__type': 'Pointer',
      'className': '_User',
      'objectId': id
    };
  }
  getPointer(id: String, className: String) {
    return {
      '__type': 'Pointer',
      'className': className,
      'objectId': id
    };
  }

  getUserItem(element) {
    const item: any = {};
    item.objectId = element.id;
    item.firstName = element.get('firstName');
    item.lastName = element.get('lastName');
    item.sex = element.get('sex');
    return item;
  }

  getUser(term: string): Observable<any> {
    return new Observable(observer => {

      const usernameQuery = new Parse.Query(Parse.User);
      usernameQuery.equalTo('p_username', term.toLowerCase());

      const emailQuery = new Parse.Query(Parse.User);
      emailQuery.equalTo('p_email', term.toLowerCase());

      const mainQuery = Parse.Query.or(usernameQuery, emailQuery);


      mainQuery.first().then((user) => {
        observer.next(user);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  userExists(term: string) {
    return this.getUser(term).map(user => {
      if (user == null) {
        return { asyncInvalid: true };
      } else {
        return null;
      }
    });
  }

  getUsersForDashboard(): Observable<UserTransferModel> {
    const role: string = this.ps.currentUser.get('role');
    const query = new Parse.Query(Parse.User);
    switch (role) {
      case 'parent':
        return this.getChildrenForParents();
      case 'teacher':
        return this.getUsersForTeacher();
      case 'admin':
        return this.getStudentsByAdminQuery();
      default:
        return Observable.of(INIT_USER_TRANSFER);
    }
  }

  getChildrenForParents(): Observable<UserTransferModel> {
    const utm: UserTransferModel = INIT_USER_TRANSFER;
    return this.getChildren()
      .flatMap(users => Observable.from(users))
      .map(user => this.getUserVM(user))
      .toArray()
      .map(users => {
        utm.students = users;
        return utm;
      });
  }
  getUsersForTeacher(): Observable<UserTransferModel> {
    const utm: UserTransferModel = <UserTransferModel>{
      students: [],
      parents: [],
      teachers: [],
      admins: []
    };
    return this.getStudentByTeacherQuery().flatMap(students => {
      utm.students = students;
      return this.getParentsByTeacherQuery(students);
    })
    .map(parents => {
      utm.parents = parents;
      return utm;
    });
  }

  getParentsByTeacherQuery(students: IUser[]): Observable<IUser[]> {
    return Observable.from(students)
    .flatMap(student => {
        return this.getProfileByUser(student);
      })
      .filter(parent => parent.role === 'parent')
      .toArray()
      .map(parents => _.uniq(parents));
  }

  getProfileByUser(user: IUser): Observable<IUser> {
    return new Observable(observer => {
      const pQ = new Parse.Query('Profile');
      pQ.equalTo('user', this.getUserPointer(user.objectId));
      pQ.include(['parent']);
      pQ.first().then((p) => {
        observer.next(this.getUserVM(p.get('parent')));
        observer.complete();
      }, err => {
        console.log(err);
        observer.error(err);
      });
    });
  }

  getStudentByTeacherQuery(): Observable<IUser[]> {
    const classRoomQuery = new Parse.Query(ClassRoom);
    classRoomQuery.equalTo('teacher', this.ps.currentUser);
    return new Observable(observer => {
      classRoomQuery.first().then((classRoom) => {
        observer.next(classRoom);
        observer.complete();
      }, err => observer.error(err));
    })
    .flatMap((cr: any) => {
      let sArray: string[] = [];
      if (cr) {
        sArray = cr.get('students');
      }
      return Observable.from(sArray);
    })
    .flatMap((id: string) => this.getUserById(id))
    .toArray();
  }

  getStudentsByAdminQuery(): Observable<UserTransferModel> {
    const utm = INIT_USER_TRANSFER;
    const school = this.ps.currentUser.get('school');
    const query = new Parse.Query(Parse.User);
    query.equalTo('school', school);
    query.ascending('createdAt');
    query.limit(999);
    return new Observable(observer => {
      query.find().then(users => {
        observer.next(users);
        observer.complete();
      }, err => observer.error(err));
    })
    .flatMap((users: any[]) => Observable.from(users))
    .map(user => this.getUserVM(user))
    .toArray()
    .map(
      users => {
        _.each(users, user => {
          switch (user.role) {
            case 'user':
              utm.students.push(user);
              break;
            case 'parent':
              utm.parents.push(user);
              break;
            case 'teacher':
              utm.teachers.push(user);
              break;
            case 'admin':
              utm.admins.push(user);
              break;
          }
        });
        return utm;
      }
    );
  }

  getUserById(id: string): Observable<IUser> {
    const userQuery = new Parse.Query(Parse.User);
    userQuery.equalTo('objectId', id);
    userQuery.include(['profile']);
    return new Observable(observer => {
      userQuery.first().then((user) => {
        observer.next(user);
        observer.complete();
      }, err => observer.error(err));
    }).map(user => this.getUserVM(user));
  }

  getUserVM(user: any): IUser {
    if (!user) {
      return <IUser> {};
    }
    return <IUser>{
      objectId: user.id,
      username: user.get('username'),
      firstName: user.get('firstName'),
      lastName: user.get('lastName'),
      email: user.get('email'),
      role: user.get('role'),
      sex: user.get('sex'),
      createdAt: user.get('createdAt'),
      avatar: user.has('img') ? user.get('img').url() : getSrc(user.get('email'))
    };
  }
}
export interface IUserReq {
  role: string;
  limit: number;
  skip: number;
}
export const ADD_USER_COUNT_ACTION = 'ADD_USER_COUNT_ACTION';
export class AddUserCountActions implements Action {
  readonly type: string = ADD_USER_COUNT_ACTION;
  constructor(public payload?: {count: number; role: string}) {}
}
export const GET_USER_COUNT_ACTION = 'GET_USER_COUNT_ACTION';
export class GetUserCountActions implements Action {
  readonly type: string = GET_USER_COUNT_ACTION;

  /**
   * Creates an instance of GetUserCountActions.
   * @param {string} [payload] is the user role you want to query
   * @memberof GetUserCountActions
   */
  constructor(public payload?: string) {}
}

export function addUserCountReducer(
  state: AppState, action: AddUserCountActions
): AppState {
  const newState = _.cloneDeep(state);
  const role = action.payload.role;
  switch (role) {
    case 'user':
      newState.uiState.totalCounts.studentCount = action.payload.count;
      break;
    case 'parent':
      newState.uiState.totalCounts.parentCount = action.payload.count;
      break;
    case 'teacher':
      newState.uiState.totalCounts.teacherCount = action.payload.count;
      break;
    case 'admin':
      newState.uiState.totalCounts.adminCount = action.payload.count;
      break;
  }
  return newState;
}



