import { IReport } from './../model/IReport';
import { INews } from './../model/INews';
import { IMail } from './../model/IMessage';
import { IUser } from '../model/IUser';
export interface DataState {
  outbox: { [key: string]: IMail };
  inbox: { [key: string]: IMail };
  news: { [key: string]: INews };
  users: UsersState;
}

export interface UsersState {
  students: { [key: string]: IUser };
  parents: { [key: string]: IUser };
  teachers: { [key: string]: IUser };
  admins: { [key: string]: IUser };
  currentSelectedUserProfile: {
    user: IUser,
    parent: IUser,
    reports: IReport[]
  };
}

export const INIT_USER_PROFILE = {
  user: undefined,
  parent: undefined,
  reports: []
};

export const INIT_USERS_STATE: UsersState = {
  students: {},
  parents: {},
  teachers: {},
  admins: {},
  currentSelectedUserProfile: INIT_USER_PROFILE
};

export const INITIAL_MESSAGE_DATA_STATE: DataState = {
  inbox: {},
  outbox: {},
  users: INIT_USERS_STATE,
  news: {}
};



