import { AppState } from './../../message/message-state';
import * as ProfileActions from './profile.actions';
import * as _ from 'lodash';
import { IPickupVM } from '../../model/IPickupVM';
export function AddNewPickupReducer(
  state: AppState, action: ProfileActions.AddNewPickup): AppState {

  const newState = _.cloneDeep(state);
  newState.profile.pickupState.pickup = action.payload.pickup;
  newState.profile.pickupState.dirty = true;
  return newState;
}

export function NewPickupAddedReducer(
  state: AppState, action: ProfileActions.NewPickupAdded) {

  const newState = _.cloneDeep(state);
  newState.profile.pickupState.pickup = action.payload;
  newState.profile.pickupState.dirty = false;
  const pickups: IPickupVM[] = _.values(newState.profile.pickupState.pickups);
  pickups.push(action.payload);
  newState.profile.pickupState.pickups = _.keyBy(pickups, 'objectId');
  return newState;
}

export function AddAllPickupsReducer(
  state: AppState, action: ProfileActions.AddAllPickups
): AppState {
  const newState = _.cloneDeep(state);
  newState.profile.pickupState.pickups = _.keyBy(action.payload, 'objectId');
  return newState;
}

export function SetCurrentSelectedUserReducer(
  state: AppState,
  action: ProfileActions.SetCurrentSelectedUser
) {
  const newState = _.cloneDeep(state);
  newState.profile.currentSelectedUser = action.payload;
  return newState;
}



