import { LoadViewMessageAction } from './../../../message/message-action';
import { Store } from '@ngrx/store';
import { MomentUtil } from './../../../moment-util';
import { Component, OnInit, Input } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { MessageVM } from '../../../message/message-vm';
import { AppState } from '../../../message/message-state';

@Component({
  selector: 'sp-message-list',
  templateUrl: './message-list.component.html',
  styleUrls: ['./message-list.component.css']
})
export class MessageListComponent implements OnInit {

  @Input()
  messages: MessageVM[];
  @Input()
  section:number;
  moment = new MomentUtil().getMoment();

  constructor(private store:Store<AppState>) { }

  ngOnInit() {
    this.store.subscribe(
      state => {
        this.section = state.uiState.currentMailSection;
      }
    );
  }

  formatTime(date) {
    return this.moment(date).fromNow();
  }

  viewMessage(id) {
    this.store.dispatch(new LoadViewMessageAction({id:id,section:this.section}));
  }

}



