import { ReportsFetchedAction } from './../../actions/report-actions';
import { AppState } from './../../message/message-state';
import * as _ from 'lodash';
export function ReportsFetched(state: AppState, action: ReportsFetchedAction): AppState {
  const newState = _.cloneDeep(state);

  newState.dataState.users.currentSelectedUserProfile.reports = action.payload;

  return state;
}



