import { ToastModule, ToastOptions } from 'ng2-toastr';
import { NewClassDialogComponent } from '../commons/widget/new-class-dialog/new-class-dialog.component';
import { RouterModule } from '@angular/router';
import { WidgetModule } from './../commons/widget/widget.module';
import { RouteService } from './../services/route.service';
import { SearchService } from './../services/search.service';
import { LoadingService } from '../services/loading.service';
import { ParseService } from './../services/parse.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { KlassComponent } from './klass.component';
import {
  MatButtonModule,
  MatIconModule,
  MatIconRegistry,
  MatDialogModule,
  MatMenuModule,
  MatProgressBarModule,
  MatCardModule,
  MatListModule,
  MatInputModule
} from "@angular/material";
import { KlassService } from '../services/klass.service';
import { EditClassDialogComponent } from '../commons/widget/edit-class-dialog/edit-class-dialog.component';
import { DeleteClassDialogComponent } from '../commons/widget/delete-class-dialog/delete-class-dialog.component';
import { KlassListComponent } from './klass-list/klass-list.component';
import { KlassViewComponent } from './klass-view/klass-view.component';
import { CustomOption } from '../toastr-options';
import { KlassResolver } from '../resolvers/klass.resolver';
import { UserService } from '../services/user.service';
import { AssignTeacherDialogComponent } from '../commons/widget/assign-teacher-dialog/assign-teacher-dialog.component';
import { ComposeDialogComponent } from '../commons/widget/compose-dialog/compose-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    MatIconModule,
    MatButtonModule,
    WidgetModule,
    MatMenuModule,
    MatDialogModule,
    MatProgressBarModule,
    MatCardModule,
    MatListModule,
    MatInputModule,
    RouterModule,
    ToastModule.forRoot(),
    MatListModule
  ],
  declarations: [KlassComponent, KlassListComponent, KlassViewComponent],
  providers: [
    ParseService,
    LoadingService,
    SearchService,
    KlassService,
    KlassResolver,
    RouteService,
    MatIconRegistry,
    UserService,
    { provide: ToastOptions, useClass: CustomOption }
  ],
  entryComponents: [
    EditClassDialogComponent,
    DeleteClassDialogComponent,
    NewClassDialogComponent,
    AssignTeacherDialogComponent,
    ComposeDialogComponent
  ]
})
export class KlassModule {}



