import { GET_USER_COUNT_ACTION } from './../../user.service';
import { NewsService } from '../../news.service';
import { DashboardData, INIT_DASHBOARD_DATA } from '../../../model/user-transfer-model';
import { MessageSentAction, MAIL_LOADED_ACTION } from './../../../message/message-action';
import { MessageService } from './../../message.service';
import { Injectable } from '@angular/core';
import { Actions, Effect } from '@ngrx/effects'
import { INIT_ACTION, MailLoadedAction, SEND_MESSAGE_ACTION } from '../../../message/message-action';
import { Action } from '@ngrx/store';
import { Observable } from 'rxjs/Rx';
import { UserService, AddUserCountActions } from '../../user.service';
import { LoadingService } from '../../loading.service';
import { GET_NEWS_ACTION, AddNewsAction } from '../../../news/news.component';

@Injectable()
export class LoadMailServiceEffect {

  dbd: DashboardData = INIT_DASHBOARD_DATA;
  role: string;

  @Effect() userMails$ = this.actions$
    .ofType(INIT_ACTION)
    .do(() => this.ls.setLoading(true))
    .switchMap(() => {
      this.ls.setLoading(true);
      return this.ms.getCombinedMailObservable();
    })
    .do(mails => this.dbd.mails = mails)
    .flatMap(() => this.us.getUsersForDashboard())
    .flatMap(users => {
      this.dbd.users = users;
      this.ls.setLoading(false);
      return this.ns.getNews();
    })
    .map(news => {
      this.dbd.news = news;
      return new MailLoadedAction(this.dbd);
    })
    .do(() => this.ls.setLoading(false));

  @Effect() sendMail$ = this.actions$
    .ofType(SEND_MESSAGE_ACTION)
    .do(() => this.ls.setLoading(true))
    .switchMap(action => this.ms.sendMail(action.payload))
    .map(mail => new MessageSentAction(mail))
    .do(() => this.ls.setLoading(false));

  @Effect() getUserCount$ = this.actions$
    .ofType(GET_USER_COUNT_ACTION)
    .do(action => this.role = action.payload)
    .switchMap(action => this.us.getUserCount(action.payload))
    .map(count => new AddUserCountActions({count: count, role: this.role}));

  @Effect() getNews$ = this.actions$
    .ofType(GET_NEWS_ACTION)
    .switchMap(action => this.ns.getNewsItems())
    .debug()
    .map(news => new AddNewsAction(news));

  constructor(
    private ms: MessageService,
    private us: UserService,
    private ns: NewsService,
    private ls: LoadingService,
    private actions$: Actions) {
  }

}



