import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { FormControl, FormBuilder, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs/Rx';
import { UserService } from '../../../services/user.service';

@Component({
  selector: 'sp-assign-teacher-dialog',
  templateUrl: './assign-teacher-dialog.component.html',
  styleUrls: ['./assign-teacher-dialog.component.css']
})
export class AssignTeacherDialogComponent implements OnInit {

  teachers: any[] = [
    {
      objectId: "ksvksdnlsd",
      firstName: "Thompson",
      lastName: "Edolo",
      email: "verygreenboi"
    }
  ];

  dataset:any[] = [];

  term = new FormControl('');
  searchForm:FormGroup = this.builder.group({
    term: this.term
  });

  constructor(
    public assignTeacherDialogRef: MatDialogRef<AssignTeacherDialogComponent>, private builder:FormBuilder, private us: UserService) { }

  ngOnInit() {
    this.searchForm.valueChanges.debounceTime(500).distinctUntilChanged().switchMap(t => {
      console.log(t);
      return this.us.searchUserByTerm(t.term);
    }).retry(3).subscribe(
      (ter:any[]) => {
        console.log(ter);
        this.dataset = ter;
      }
    );
  }

  assign(id) {
    this.assignTeacherDialogRef.close({
      teacher: id
    });
  }

}



