import { ParseService } from './services/parse.service';
import { AppState} from './message/message-state';
import { Subscription } from 'rxjs/Rx';
import { RouteService } from './services/route.service';
import { Component, OnInit, NgZone, Renderer, ElementRef, ViewChild } from '@angular/core';
import { Event as RouterEvent, NavigationStart, Router, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';
import { Store } from '@ngrx/store';
import { MessageService } from './services/message.service';
import { LoadDashBoardAction } from './message/message-action';

declare var jQuery: any;

@Component({
  selector: 'body',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app works!';
  sub: Subscription;

  @ViewChild('spinnerElement') spinnerElement: ElementRef;

  constructor(
    private rs: RouteService,
    private inboxStore: Store<AppState>,
    private router: Router,
    private ngZone: NgZone,
    private renderer: Renderer,
    private ms: MessageService,
    private ps: ParseService
    ) {
    this.sub = router.events.subscribe((event: RouterEvent) => {
      this._navigationInterceptor(event);
    });
  }

  ngOnInit() {
    const body = jQuery('body');
    body.attr('class', '');
    body.addClass('sticky-header');
    this.rs.bc.subscribe(
      (c) => {
        body.attr('class', '');
        body.addClass(c);
      }
    );
    if (this.ps.currentUser) {
      this.inboxStore.dispatch(new LoadDashBoardAction());
    }
  }

  _navigationInterceptor(event){
    if (event instanceof NavigationStart) {
      // We wanna run this function outside of Angular's zone to
      // bypass change detection
      this.ngZone.runOutsideAngular(() => {
        const ne = jQuery(this.spinnerElement.nativeElement);
        ne.show();
      });
    }

    if (event instanceof NavigationEnd) {
      this._hideSpinner();
    }

    if (event instanceof NavigationCancel) {
      this._hideSpinner();
    }

    if (event instanceof NavigationError) {
      this._hideSpinner();
    }
  }

  private _hideSpinner(): void {
    // We wanna run this function outside of Angular's zone to
    // bypass change detection,
    this.ngZone.runOutsideAngular(() => {
      const ne = jQuery(this.spinnerElement.nativeElement);
      ne.hide();
    });
  }

}



