import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Rx';

import { UserService } from '../../../services/user.service';
import { IUser } from '../../../model/IUser';

@Component({
  selector: 'sp-link-parent',
  templateUrl: './link-parent.component.html',
  styleUrls: ['./link-parent.component.css']
})
export class LinkParentComponent implements OnInit {

  linkParentForm: FormGroup;

  parent$: Observable<IUser>;
  parent: IUser;

  constructor(
    public linkParentDialogRef: MatDialogRef<LinkParentComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private fb: FormBuilder,
    private us: UserService
  ) {

    this.linkParentForm = fb.group({
      term: ['', Validators.required]
    });

  }

  ngOnInit() {
  }

  search() {
    const term = this.linkParentForm.value.term;
    this.us.searchUserByTerm(term, 'parent')
      .filter((u: any[]) => u.length > 0)
      .map(u => this.us.getUserVM(u[0]))
      .subscribe(user => this.parent = user);
  }

  link() {
    if (parent) {
      this.linkParentDialogRef.close({
        parent: this.parent
      });
    }
  }

}



