import { IUser } from './../../../model/IUser';
import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'sp-up-widget',
  templateUrl: './u-pwidget.component.html',
  styleUrls: ['./u-pwidget.component.css']
})
export class UPWidgetComponent implements OnInit {

  @Input()
  user?: IUser;

  @Input()
  showFooter = true;

  @Input('fees-paid')
  fStatus: boolean;


  source: String = 'assets/img/mat-bg-1.jpg';


  constructor(private router: Router) { }

  ngOnInit() {
  }

  getFullname(): string {
    if (this.user) {
      return this.user.firstName + ' '  + this.user.lastName;
    } else {
      return ' ';
    }
  }

  navigateToUser() {
    this.router.navigate(['/profile', this.user.objectId]);
  }

}



