export interface UIState {
  currentUnread: number;
  currentMailSection: number;
  totalCounts: UITotalCounts;
}

export interface UITotalCounts {
  studentCount: number;
  parentCount: number;
  teacherCount: number;
  adminCount: number;
}

export const INIT_TOTAL_COUNTS: UITotalCounts = {
  studentCount: 0,
  parentCount: 0,
  teacherCount: 0,
  adminCount: 0
};

export const INITIAL_MESSAGE_UI_STATE: UIState = {
  currentUnread: undefined,
  currentMailSection: 0,
  totalCounts: INIT_TOTAL_COUNTS
};



