import { ParseService } from './../services/parse.service';
import { AppState } from './../message/message-state';
import { INews } from './../model/INews';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { Store, Action } from '@ngrx/store';
import * as _ from 'lodash';
import { WindowRefService } from '../services/window-ref.service';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {

  news$: Observable<INews[]>;
  role: string = this.ps.currentUser.get('role');
  private _window: Window;

  constructor(private store: Store<AppState>, private ps: ParseService, windowRef: WindowRefService) {
    this._window = windowRef.nativeWindow;
  }

  ngOnInit() {
    this.news$ = this.store.select(state => <INews[]>_.values(state.dataState.news));
    this.store.dispatch(new GetNewsAction(this.role));
    this._window.scroll(0, 0);
  }

}

export const GET_NEWS_ACTION = 'GET_NEWS_ACTION';
export const ADD_NEWS_ACTION = 'ADD_NEWS_ACTION';

export class GetNewsAction implements Action {
  readonly type: string = GET_NEWS_ACTION;
  constructor(payload?: string) {}
}

export class AddNewsAction implements Action {
  readonly type: string = ADD_NEWS_ACTION;
  constructor(public payload?: INews[]) {}
}

export function addNewsReducer(state: AppState, action: AddNewsAction): AppState {
  const newState = _.cloneDeep(state);
  newState.dataState.news = _.keyBy(action.payload, 'objectId');
  return newState;
}



