import { IReport } from './../../model/IReport';
import { ISchool } from './../../model/ISchool';
import { IUser } from './../../model/IUser';
import { IPickupVM } from './../../model/IPickupVM';
import { Action } from '@ngrx/store';
export const ADD_NEW_PICKUP = 'ADD_NEW_PICKUP';
export const NEW_PICKUP_ADDED = 'NEW_PICKUP_ADDED';
export const GET_PICKUP_ACTION = 'GET_PICKUP_ACTION';
export const ADD_ALL_PICKUPS = 'ADD_ALL_PICKUPS';
export const SET_CURRENT_SELECTED_USER = 'SET_CURRENT_SELECTED_USER';
export const SET_CURRENT_SELECTED_USER_RESULTS = 'SET_CURRENT_SELECTED_USER_RESULTS';
export const CURRENT_SELECTED_USER_RESULTS_SET = 'CURRENT_SELECTED_USER_RESULTS_SET';

export class AddNewPickup implements Action {
  readonly type: string = ADD_NEW_PICKUP;
  constructor(public payload?: {pickup: IPickupVM; file: File}) {}
}

export class NewPickupAdded implements Action {
  readonly type: string = NEW_PICKUP_ADDED;
  constructor(public payload?: IPickupVM) {}
}

export class GetPickupAction implements Action {
  readonly type: string = GET_PICKUP_ACTION;
  constructor(public payload?: {parent: IUser; school: ISchool}) {}
}

export class AddAllPickups implements Action {
  readonly type: string = ADD_ALL_PICKUPS;
  constructor(public payload?: IPickupVM[]) {}
}

export class SetCurrentSelectedUser implements Action {
  readonly type: string = SET_CURRENT_SELECTED_USER;
  constructor(public payload?: IUser) {}
}

export class SetCurrentSelectedUserResults implements Action {
  readonly type: string = SET_CURRENT_SELECTED_USER_RESULTS;
  constructor(public payload?: IUser) {}
}
export class CurrentSelectedUserResultsSet implements Action {
  readonly type: string = CURRENT_SELECTED_USER_RESULTS_SET;
  constructor(public payload?: IReport[]) {}
}

export type ProfileActions = AddNewPickup |
NewPickupAdded |
GetPickupAction |
SetCurrentSelectedUser |
SetCurrentSelectedUserResults |
CurrentSelectedUserResultsSet |
AddAllPickups;



