import { IReport } from './../model/IReport';
import { IUser } from './../model/IUser';
import { Action } from '@ngrx/store';

export const FETCH_REPORT_ACTION = 'FETCH_REPORT_ACTION';
export const REPORTS_FETCHED_ACTION = 'REPORTS_FETCHED_ACTION';

export class FetchReportAction implements Action {
  readonly type: string = FETCH_REPORT_ACTION;
  constructor(public payload?: IUser) {}
}

export class ReportsFetchedAction implements Action {
  readonly type: string = REPORTS_FETCHED_ACTION;
  constructor(public payload?: IReport[]) {}
}



