import { WindowRefService } from './services/window-ref.service';
import { ReportService } from './services/report.service';
import { NewsService } from './services/news.service';
import { PickupService } from './services/pickup.service';
import { IsActivatedGuard } from './guards/is-activated.guard';
import { LayoutModule } from './commons/layout/layout.module';
import { ProfileEffectService } from './profile/store/profile-effect.service';
import { SettingsModule } from './settings/settings.module';
import { UserEffectService } from './services/store/effect/effect.service';
import { RouterStoreModule } from '@ngrx/router-store';
import { ServerNotificationEffectService } from './services/store/effect/server-notification-effect.service';
import { LoadMailServiceEffect } from './services/store/effect/load-mail.service';
import { DirectivesModule } from './commons/directives/directives.module';
import { INITIAL_APP_STATE} from './message/message-state';
import { SchoolModule } from './school/school.module';
import { WidgetModule } from './commons/widget/widget.module';
import { RouteService } from './services/route.service';
import { ROUTES } from './routing';
import { PreloadAllModules, RouterModule } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { LoginModule } from './login/login.module';
import { ErrorsModule } from './errors/errors.module';
import { EventModule } from './event/event.module';
import { ForgotPasswordModule } from './forgot-password/forgot-password.module';
import { KlassModule } from './klass/klass.module';
import { MessageModule } from './message/message.module';
import { NewsModule } from './news/news.module';
import { RegisterModule } from './register/register.module';
import { UserModule } from './user/user.module';
import { BasicComponent } from './commons/layout/basic/basic.component';
import { BlankComponent } from './commons/layout/blank/blank.component';
import { DemoComponent } from './demo/demo.component';
import { messageStoreReducer} from './message/reducers/inbox-reducer';
import { StoreModule } from '@ngrx/store';
import { MessageService } from './services/message.service';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { ViewMailServiceEffect } from './services/store/effect/view-mail.service';
import { ReportEffectService } from './services/store/effect/report-effect';
import { NotActiveComponent } from './not-active/not-active.component';
import { AuthenticatedGuard } from './guards/authenticated.guard';
import { Http, HttpModule } from '@angular/http';
import { ParseService } from './services/parse.service';

@NgModule({
  declarations: [
    AppComponent,
    DemoComponent,
    NotActiveComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    LayoutModule,
    LoginModule,
    ErrorsModule,
    EventModule,
    ForgotPasswordModule,
    KlassModule,
    RegisterModule,
    SettingsModule,
    WidgetModule,
    DirectivesModule,
    RouterModule.forRoot(ROUTES),
    StoreModule.provideStore(messageStoreReducer),
    EffectsModule.run(LoadMailServiceEffect),
    EffectsModule.run(ViewMailServiceEffect),
    EffectsModule.run(ServerNotificationEffectService),
    EffectsModule.run(UserEffectService),
    EffectsModule.run(ReportEffectService),
    StoreDevtoolsModule.instrumentOnlyWithExtension()
  ],
  providers: [
    RouteService,
    MessageService,
    AuthenticatedGuard,
    NewsService,
    IsActivatedGuard,
    PickupService,
    ParseService,
    ReportService,
    WindowRefService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



