import { Effect } from '@ngrx/effects';
import { StreamNewMessageAction } from './../../../message/message-action';
import { Injectable } from '@angular/core';
import { MessageService, LiveMessageModel } from '../../message.service';

@Injectable()
export class ServerNotificationEffectService {
  @Effect() newMessage$ = this.ms.newMessageLiveQuery()
    .map((message: LiveMessageModel) => new StreamNewMessageAction(message));

  constructor(private ms: MessageService) { }


}



