import { AppState } from './../../message/message-state';
import { SetCurrentSelectedUserAction } from './../../user/user-action';
import * as _ from 'lodash';
export function SetCurrentSelectedUser(state: AppState, action: SetCurrentSelectedUserAction): AppState {

  const newState = _.cloneDeep(state);

  newState.dataState.users.currentSelectedUserProfile.user = action.payload;

  return state;
}



