import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ColumnComponent } from '../column/column.component';
import { IEvent, EventType } from '../../../model/IEvent';

@Component({
  selector: 'sp-datatable',
  templateUrl: './datatable.component.html',
  styleUrls: ['./datatable.component.css']
})
export class DatatableComponent implements OnInit {

  @Input() dataset;
  columns: ColumnComponent[] = [];

  @Input()
  deletable = false;

  @Input()
  editable = false;

  @Input()
  viewable = false;

  @Input("parse-table")
  parse = false;

  @Output()
  event: EventEmitter<IEvent> = new EventEmitter<IEvent>();

  constructor() { }

  ngOnInit() {
  }

  addColumn(column) {
    this.columns.push(column);
  }

  editRow(id) {
    const e: any = {};

    e.objectId = id;
    e.event = EventType.edit;

    this.event.emit(e);
  };

  deleteRow(id) {
    const e: any = {};

    e.objectId = id;
    e.event = EventType.delete;

    this.event.emit(<IEvent>e);
  }

  viewRow(id) {
    let e: any = {};

    e.objectId = id;
    e.event = EventType.view;

    this.event.emit(<IEvent>e);
  }


  getRemarkStyle(header, value) {
    // console.log(header, value);
    if (header === 'Remark') {
      if (value === 'Failed') {
        return 'red-tx';
      } else if (value === 'Passed') {
        return 'green-tx';
      } else {
        return '';
      }
    }
  }

}



