import { Store } from '@ngrx/store';
import { MessageService } from './../../../services/message.service';
import { ParseService } from '../../../services/parse.service';
import { Component, OnInit } from '@angular/core';
import { hover, toggleSidebar, visibleSubMenuClose } from '../../../app.js.helpers';
import { AppState } from '../../../message/message-state';

declare var jQuery: any;

@Component({
  selector: 'sp-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  uc: number;
  ps = this.parse;
  user = this.ps.currentUser;
  isParent = this.user.get('role') === 'parent';
  isStudent = this.user.get('role') === 'user';
  isTeacher = this.user.get('role') === 'teacher';
  isAdmin = this.user.get('role') === 'admin';

  constructor(private parse: ParseService, private ms: MessageService, private store: Store<AppState>) {
    store.subscribe(
      state => {
        this.uc = state.uiState.currentUnread;
      }
    );
  }

  ngOnInit() {
    visibleSubMenuClose();
    toggleSidebar();
    hover();
  }

}



