import { AppState } from './../../../message/message-state';
import { Store } from '@ngrx/store';
import { IUser } from './../../../model/IUser';
import { IMail } from './../../../model/IMessage';
import { Component, OnInit, Input } from '@angular/core';
import { ParseService } from '../../../services/parse.service';
import * as MD5 from './../../../md5';
import { MarkAsReadAction } from '../../../message/message-action';

@Component({
  selector: 'sp-view-message',
  templateUrl: './view-message.component.html',
  styleUrls: ['./view-message.component.css']
})
export class ViewMessageComponent implements OnInit {

  @Input()
  mail: IMail;

  subject: string;
  message: string;
  isFromMe: boolean;

  hash: string;
  fullname: string;
  email: string;

  // TODO: Dispatch MESSAGE_READ_ACTION

  constructor(private ps: ParseService, private store: Store<AppState>) { }

  ngOnInit() {
    this.isFromMe = this.ps.currentUser.id == this.mail.from.objectId ? true : false;
    this.subject = this.mail.subject;
    this.message = this.mail.message.text;

    if (this.isFromMe) {
      this.setPersonalInfo(this.mail.to);
    } else {
      this.setPersonalInfo(this.mail.from);
      if (!this.mail.isRead)
        this.store.dispatch(new MarkAsReadAction(this.mail.objectId));
    }
  }

  setPersonalInfo(user: IUser) {
    this.email = user.email;
    this.fullname = user.firstName + " " + user.lastName;
    this.hash = this.getHash(this.email);
  }

  getHash(email: string): string {
    const baseUrl = 'https://www.gravatar.com/avatar/';
    const hash = MD5.encrypt(email);
    return baseUrl + hash;
  }

}



